.video_panel {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  overflow: hidden;
  @media (max-width: $screen-md) {
    //margin-bottom: 156px;
  }

  #video-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      background: rgba(21, 14, 27, 0.72);
    }

    iframe {
      box-sizing: border-box;
      height: 56.25vw;
      left: 50%;
      min-height: 100%;
      min-width: 100%;
      transform: translate(-50%, -50%);
      position: absolute;
      top: 50%;
      width: 177.77777778vh;
    }
  }

  .small_title {
    @include fig(500, 23, 28px);
    color: #fff;
    text-align: center;
    margin-bottom: 24px;
  }

  .big_title {
    @include pop(400, 35, 43px);
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.25) 0%,
      #ffffff 0.01%,
      rgba(255, 255, 255, 0.6) 100%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-align: center;
    margin-bottom: 24px;
    letter-spacing: -0.02em;
    margin-bottom: 48px;
    @media (min-width: $screen-md) {
      font-size: 53px;
      line-height: 64px;
    }
  }

  .btn_group {
    align-items: center;
    justify-content: center;
    button {
      background: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      @include pop(400, 15, 15px);
      letter-spacing: 0.01em;
      color: #000;
      padding: 0 24px;
      min-height: 56px;
      border-radius: 6px;

      .icon {
        display: flex;
        align-items: center;
        justify-content: center;
        max-width: 16px;
        margin-right: 12px;
      }
    }
  }

  .video_modal {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 999;
    background: rgba(13, 11, 15, 0.4);
    backdrop-filter: blur(60px);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transition: ease all 0.3s;
    z-index: 90;
    @media (min-width: $screen-md) {
      // height: 70vh;
    }

    &.active {
      opacity: 1;
      visibility: visible;
    }

    .watch_video_close {
      position: absolute;
      top: 24px;
      right: 24px;
      width: 100%;
      max-width: 20px;
      z-index: 90;
      @media (min-width: $screen-md) {
        top: 100px;
        right: 200px;
      }

      &:hover {
        cursor: pointer;
      }

      svg {
        path {
          fill: #fff;
        }
      }
    }
    .video_container {
      position: relative;
      width: 100%;
      padding: 20px;
      @media (min-width: $screen-md) {
        padding: 100px 0;
        position: relative;
        padding-bottom: 56.25%;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      iframe {
        position: absolute;
        top: 20%;
        width: 60%;
        height: 60%;
      }
    }
  }
}
