.large_cta_container {
  padding: 0;
  position: relative;
  z-index: 6;
  background: #fff;

  .container {
    padding: 0;
  }

  @media (min-width: $screen-md) {
    //height: 100vh;
    position: relative;
  }

  .large_cta_holder {
    padding: 140px 20px 160px 20px;
    background-size: cover;
    background-position: 50%;

    @media (min-width: $screen-md) {
      padding: 240px 80px 240px 80px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-image: none !important;
    }

    h2 {
      @include pop(400, 35, 124%, normal);
      text-align: center;
      letter-spacing: -0.02em;
      background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      margin-bottom: 12px;

      @media (min-width: $screen-md) {
        @include pop(400, 53, 124%, normal);
      }
    }

    p {
      @include fig(400, 20, 156%, normal);
      text-align: center;
      color: #5a6272;
      margin-bottom: 40px;
    }

    .cta {
      @include pop(500, 16, 100%, normal);
      padding: 0 16px;
      width: 240px;
      height: 64px;
      background: #27b989;
      box-shadow: 0px 6px 20px rgba(39, 168, 125, 0.15),
        0px 4px 8px rgba(39, 168, 125, 0.2025),
        0px 1px 3px rgba(39, 168, 125, 0.4);
      border-radius: 6px;
      display: flex;
      align-items: center;
      justify-content: center;
      letter-spacing: 0.01em;
      color: #ffffff;
      text-decoration: none;
      margin: 0 auto;
      transition: 0.4s;

      &:hover {
        background: #24a87d;
      }
    }
  }

  .flying_ball_animations {
    position: absolute;
    // top: 0;
    // left: 0;
    width: 100%;
    height: 100%;
    display: none;
    z-index: -1;

    @media (min-width: $screen-md) {
      display: block;
    }

    // .ball1 {
    //   max-width: 270px;
    //   position: absolute;
    //   bottom: 20%;
    //   left: 10%;
    // }

    // .ball2 {

    //   position: absolute;
    //   max-width: 125px;
    //   right: 20%;
    //   top: 15%;
    // }

    // .ball3 {
    //   max-width: 175px;
    //   position: absolute;
    //   right: 5%;
    //   bottom: 30%;
    // }

    // .ball4 {
    //   position: absolute;
    //   max-width: 70px;
    //   left: 5%;
    //   top: 30%;
    // }

    // .ball5 {
    //   max-width: 24px;
    //   position: absolute;
    //   right: 33%;
    //   bottom: 35%;
    // }
  }

  .soft_grad {
    position: absolute;
    content: "";
    width: 200%;
    bottom: -55%;
    left: -50%;
    display: none;
    z-index: -2;
    opacity: 0.3;

    @media (min-width: $screen-md) {
      display: block;
    }
  }
}
