.two_column_image_timeline_simple {
  position: relative;

  &.no_title_area {
    // margin-top: -120px;
    @media (min-width: $screen-md) {
      margin-top: -160px;
    }
    @media (min-width: $screen-lg) {
      margin-top: -240px;
    }

    .tctsPinnableSection .video_pin_wrapper video {
      @media (min-width: $screen-lg) {
        // height: 75vh;
      }
    }
  }
  .mobile {
    @media (min-width: $screen-md) {
      display: none;
    }
  }

  .title_area {
    background: linear-gradient(180deg, #e9ecf2 0%, #e7e3ed 100%, #e8eaed 100%);
    min-height: 100vh;
    padding: 267px 0 0;
    @media (min-width: $screen-md) {
      padding: 275px 0 0;
    }

    .container {
      padding: 0;
    }

    h3 {
      @include pop(400, 44, 49px);
      letter-spacing: -0.02em;
      @include titleGrad();
      text-align: center;
      margin-bottom: 88px;
      @media (min-width: $screen-md) {
        font-size: 65px;
        line-height: 65px;
      }

      br {
        display: none;
        @media (min-width: $screen-md) {
          display: block;
        }
      }
    }

    .title_area_image {
      overflow: hidden;
      max-height: 50vh;
      @media (min-width: $screen-md) {
        // display: none;
        position: absolute;
        width: 100%;
      }
      img {
        width: 225%;
        transform: translateX(-25%);
        @media (min-width: $screen-md) {
          width: 75%;
          transform: translateX(25%);
        }
        @media (min-width: $screen-lg) {
          width: 50%;
          transform: translateX(50%);
        }
      }
    }
  }

  .tlsmp_first {
    @media (min-width: $screen-md) {
      // transform: translateY(-50vh);
    }
  }

  .timeline_row {
    min-height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 60px 0;

    @media (max-width: 376px) {
      padding: 60px 0;
    }

    &:last-child {
      @media (min-width: $screen-sm) {
        margin-top: -100px;
      }
      @media (min-width: $screen-lg) {
        margin-top: -180px;
      }
    }

    &.align_videos_top .container {
      @media (max-width: $screen-sm) {
        align-items: flex-start;
      }

      video {
        @media (max-width: $screen-md) {
          height: 55vh !important;
        }
      }

      .col {
        @media (max-width: $screen-md) {
          max-width: 56% !important;
        }

        &.col_right {
          @media (max-width: $screen-md) {
            width: 255px !important;
            max-width: 255px !important;
          }
        }
      }
    }

    .container {
      @media (min-width: $screen-md) {
        max-width: 1140px;
      }
      @media (min-width: $screen-lg) {
        max-width: 1340px;
      }
      &:before {
        @media (min-width: $screen-md) {
          content: "";
          position: absolute;
          left: 16px;
          width: 1px;
          background: #babfc9;
          height: 100vh;
        }
      }

      img {
        width: 100%;
        height: auto;
      }
    }

    &:first-child {
      &:before {
        display: none;
      }

      .container {
        &:before {
          display: none;
        }
      }
      .col_left {
        &:before {
          content: "";
          position: absolute;
          left: 17px;
          width: 1px;
          background: #babfc9;
          height: 200%;
          top: 90px;
          @media (min-width: $screen-md) {
            top: 38px;
            left: -15px;
            height: 300%;
          }
        }

        &.no_icon {
          &:before {
            @media (max-width: $screen-md) {
              top: 42px;
            }
          }
        }
      }
    }

    &:last-child {
      @media (min-width: $screen-mlg) {
        min-height: 100vh;
      }
      &:before {
        display: none;
      }

      .container {
        &:before {
          display: none;
        }
      }

      .col_left {
        &:before {
          content: "";
          position: absolute;
          left: 17px;
          width: 1px;
          background: #babfc9;
          height: 300%;
          bottom: calc(100% - 55px);
          @media (min-width: $screen-md) {
            left: -15px;
            bottom: calc(100% - 34px);
          }
        }

        &.no_icon {
          &:before {
            @media (max-width: $screen-md) {
              bottom: calc(100% - 42px);
            }
          }
        }
      }
    }

    .container {
      padding: 0 0 0 31px;
      align-items: center;

      .col {
        width: 100%;
        max-width: 50%;
      }

      .col_left {
        margin-right: 25px;
        padding-right: 20px;
        @media (min-width: $screen-md) {
          position: relative;
          max-width: 500px;
          margin-right: 0;
          padding-right: 0;
          padding-left: 35px;
        }
      }

      .col_right {
        position: relative;
        z-index: 90;
        background-image: url(../../images/phones.png);
        // background-size: auto 100%;
        background-repeat: no-repeat;
        @media (max-width: $screen-md) {
          max-width: 43% !important;
          background-size: 100% 100%;
          margin-left: auto;
        }
        @media (min-width: $screen-md) {
          max-width: 60%;
          background-image: none;
        }

        &.is_image {
          background: none;
        }
        img {
          height: 70vh;
          width: auto;
          transform: translateX(-20%);
          @media (min-width: $screen-md) {
            height: auto;
            width: 100%;
            transform: translateX(0%);
          }
        }

        .mobile_video {
          border-radius: 35px;
          padding: 6px;
          width: auto;
          height: 70vh;
          @media (min-width: $screen-md) {
            display: none;
            opacity: 0;
          }
        }

        .mobile_image {
          @media (min-width: $screen-md) {
            display: none;
            opacity: 0;
          }
        }
      }
    }

    .no_icon {
      .icon {
        &:after {
          @media (max-width: $screen-md) {
            top: 36px;
          }
        }
      }
    }

    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      max-width: 72px;
      margin-bottom: 24px;
      position: relative;

      &:after {
        position: absolute;
        top: 30px;
        left: -17px;
        content: "";
        width: 7px;
        height: 7px;
        border-radius: 100px;
        background: #6436bf;
        z-index: 10;
        @media (min-width: $screen-md) {
          width: 8px;
          height: 8px;
          left: -53px;
        }
      }
    }

    .title {
      @include pop(400, 23, 31px);
      letter-spacing: -0.02em;
      @include titleGrad();
      margin-bottom: 24px;
      @media (min-width: $screen-md) {
        font-size: 29px;
        line-height: 35px;
        max-width: 460px;
      }
    }

    &:last-child {
      .description {
        p {
          img {
            // width: auto;

            @media (min-width: $screen-md) {
              width: auto;
              max-height: 72px;
              margin-top: 25px;
            }
          }
        }
      }
    }

    .description {
      @include fig(400, 17, 23px);
      color: #4f4b58;
      @media (min-width: $screen-md) {
        font-size: 23px;
        line-height: 28px;
        max-width: 460px;
      }

      p {
        margin-bottom: 12px;

        img {
          max-width: 345px;
          padding-top: 12px;

          @media (min-width: $screen-md) {
            width: auto;
            max-height: 72px;
          }
        }
      }

      ul {
        width: 56vw;
        margin-top: 34px;
        @media (min-width: $screen-md) {
          margin-top: 24px;
          width: 86vw;
        }
        li {
          @include fig(400, 17, 20px);
          color: #1e8f69;
          display: flex;
          margin-bottom: 12px;
          @media (min-width: $screen-md) {
            font-size: 18px;
            line-height: 20px;
            margin-bottom: 16px;
          }

          &:before {
            content: "";
            width: 100%;
            max-width: 24px;
            height: 24px;
            display: flex;
            background-image: url(../../images/svg/active-tick.svg);
            background-size: 100% auto;
            background-repeat: no-repeat;
            background-position: center;
            margin-right: 16px;
          }
        }
      }
    }

    .btn_group {
      margin-top: 42px;
      a {
        color: #6436bf;
        @include pop(400, 17, 21px);
        letter-spacing: 0.02em;
        text-transform: uppercase;
        white-space: nowrap;
        text-decoration: none;
      }
    }
  }

  .tctsPinnableSection {
    display: none;
    opacity: 0;
    visibility: hidden;

    @media (min-width: $screen-md) {
      opacity: 1;
      visibility: visible;
      height: 100vh;
      display: flex;
      align-items: center;
      justify-content: flex-start;

      .pinnableSection_img {
        width: 100%;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        position: absolute;

        &:last-child {
          //margin-bottom: -300px;
        }

        &.active {
          img,
          .video_pin_wrapper {
            opacity: 1;
            visibility: visible;
          }
        }

        &.space_first_item {
          .video_pin_wrapper {
            opacity: 1 !important;
            visibility: visible !important;
          }
        }
      }

      .video_pin_wrapper {
        background-image: url(../../images/phones.png);
        background-size: 100% 100%;
        position: absolute;
        left: 20%;
        opacity: 0;
        visibility: hidden;
        @media (min-width: $screen-md) {
          left: 40%;
        }

        video {
          //height: 60vh;
          padding: 20px 15px;
          border-radius: 45px;
          width: 300px;
          height: auto;
        }
      }

      img {
        position: absolute;
        right: 0;
        visibility: hidden;
        opacity: 0;
        transition: ease all 0.3s;
      }
    }
    @media (min-width: $screen-lg) {
      .pinnableSection_img {
        &:last-child {
          //margin-bottom: -400px;
        }
      }
    }
  }
}
