.testimonial_container {
  padding: 70px 20px 70px 20px;
  background: #eeeff2;

  .container {
    padding: 0;
  }

  @media (min-width:$screen-md) {
    padding: 140px 40px;
  }

  .title_section {

    h2 {
      @include pop(400,35,124%,normal);
      margin-bottom: 12px;
      text-align: center;
      letter-spacing: -0.02em;
      background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;

      @media (min-width:$screen-md) {
        @include pop(400,44,124%,normal);
      }

    }

    h3 {
      @include pop(400,20,156%,normal);
      color: #5A6272;
      text-align: center;
      margin-bottom: 12px;
      @media (min-width:$screen-md) {
        @include pop(400,23,136%,normal);
      }
    }

  }

  .brands_section {

    @media (min-width:$screen-md) {
      margin-top: 60px;
    }

    h3 {
      @include pop(400,20,156%,normal);
      color: #5A6272;
      text-align: center;
      margin-bottom: 40px;

      @media (min-width:$screen-md) {
        @include pop(400,23,136%,normal);
      }
    }

    .brands {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;

      @media (min-width:$screen-md) {
        flex-wrap: nowrap;
      }

      .brand {
        display: flex;
        flex-grow: 1;
        flex-basis: 50%;
        max-width: 48%;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 60px;

        @media (min-width:$screen-md) {
          justify-content: center;
          align-items: center;
        }

        img {
          max-height: 32px;
          width: 100%;
          object-fit: contain;

          @media (min-width:$screen-md) {
            width: auto;
          }

        }

      }

    }

  }

  .partner_section {
    h3 {
      @include pop(400,23,156%,normal);
      color: #5A6272;
      text-align: center;
      margin-bottom: 40px;

      @media (min-width:$screen-md) {
        @include pop(400,23,136%,normal);
      }
    }

    .partner_logos {
      display: flex;
      flex-wrap: wrap;
      @media (min-width: $screen-md) {
        flex-wrap: nowrap;
      }
    }

    .logo {
      width: 100%;
      max-width: 50%;
      display: flex;
      align-items: center;
      margin-bottom: 40px;
      // justify-content: center;

      @media (min-width: $screen-md) {
        justify-content: center;
      }

      img {
        width: auto;
        max-height: 28px;
      }
    }
  }

}