.softphone_demo {
  position: fixed;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #fff;
  z-index: 999999999;
  overflow: scroll;

  @media (min-width: $screen-md) {
    flex-direction: row;
    overflow: hidden;
  }

  .gform_wrapper {
    width: 100%;

    .validation_message {
      padding: 0 !important;
      border: 0 !important;
      background: 0 !important;
      margin-top: 0px !important;
    }

  }

  .gfield--type-checkbox {
    margin-top: 28px !important;
  }
  .gfield {
    input {
      margin-bottom: 0 !important;
    }
  }

  .frm_forms {
    width: 100%;

    .frm_page_num_2 {

      #field_demo_date_label {
        text-align: center;
        margin-bottom: 22px;
        @include pop(500, 29, 124%, normal);
        color: #000;
      }

      .frm_form_title {
        display: none;
      }

      .frm_submit {
        text-align: center;

        .frm_prev_page {
          display: none;
        }

        .frm_button_submit {
          @include pop(500, 16, 124%, normal);
          margin-top: 0;
          width: 100%;
          max-width: 450px;
        }

      }

    }

  }

  .form_section {
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 32px 16px;
    background: #EEEFF2;

    @media (min-width: $screen-md) {
      max-width: 44%;
      padding: 0 72px;
    }

    .logo {
      max-width: 180px;
      margin: 0 auto;
      margin-bottom: 20px;

      @media (min-width: $screen-md) {
        margin: 0 0 20px 0;
      }
    }

    h2,h3 {
      @include pop(400, 29, 124%, normal);
      letter-spacing: -0.56px;
      margin-bottom: 12px;
      color: #000;

      @media (min-width: $screen-md) {
        @include pop(400, 32, 136%, normal);
      }

    }

    .gform_required_legend {
      display: none;
    }

    .gfield_html_formatted {
      p {
        display: none;
      }
    }

    fieldset, .gfield {
      padding: 0;
      margin: 0;
      border: 0;

      label {
        display: none;
        @include pop(400, 15, 100%, normal);
        color: #000;
        margin-bottom: 8px;

        @media (min-width: $screen-md) {
          @include pop(400, 20, 136%, normal);
          color: #000;
        }

      }

      input[type='text'],input[type='email'] {
        width: 100% !important;
        height: 52px;
        padding-left: 20px;
        border-radius: 0px;
        border: 2px solid #BABFC9;
        margin-bottom: 12px;
        @include fig(400, 23, 156%, normal);
        color: #8C8698;

        &::placeholder {
          @include fig(400, 23, 156%, normal);
          color: #8C8698;
        }

        @media (min-width: $screen-md) {
          height: 68px;
        }

      }


      .vertical_radio {
        margin-top: 28px;

        .frm_primary_label {
          display: block;
          @include pop(400, 15, 100%, normal);
          color: #000;
          margin-bottom: 16px;

          @media (min-width: $screen-md) {
            @include pop(400, 20, 136%, normal);
            color: #000;
          }

        }

        label {
          @include fig(400, 20, 80%, normal);
          color:rgba(0, 0, 1, 0.64);
        }

        .frm_radio {
          margin-bottom: 20px;
        }

        input[type="radio"] {
          appearance: none;
          -webkit-appearance: none;
          -moz-appearance: none;
          position: relative;
          width: 20px;
          height: 20px;
          margin-bottom: -4px;
        }

        input[type="radio"]::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: 35%;
          border: 4px solid #babfc9;
        }

        input[type="radio"]:checked::before {
          border-color: #7A50CE;
        }

        input[type="radio"]::after {
          content: "";
          position: absolute;
          top: 5px;
          left: 5px;
          width: 10px;
          height: 10px;
          border-radius: 35%;
          background-color: #fff;
        }

        input[type="radio"]:checked::after {
          background-color: #7A50CE;
        }

      }


      .frm_button_submit {
        margin-top: 12px;
        border-radius: 6px;
        background: #27B989;
        border: none;
        padding: 20px 24px;
        @include pop(500, 17, 100%, normal);
        color: #fff;
        transition: 0.4s;
        width: 100%;

        @media (min-width: $screen-md) {
          margin-top: 34px;
          width: unset;
          @include pop(500, 23, 100%, normal);
        }

        &:hover {
          background: #219a74;
        }

      }

      .frm_error {
        color: red;
        top: -11px;
        position: relative;
      }

    }

    .gform_previous_button {
      display: none !important;
    }

    .gform-theme-button, .gform_button {
      margin-top: 12px;
      border-radius: 6px;
      background: #27B989;
      border: none;
      padding: 20px 24px;
      @include pop(500, 17, 100%, normal);
      color: #fff;
      transition: 0.4s;
      width: 100%;
      cursor: pointer;

      @media (min-width: $screen-md) {
        margin-top: 12px;
        width: unset;
        @include pop(500, 23, 100%, normal);
      }

      &:hover {
        background: #219a74;
      }

    }


    .ginput_container_checkbox {

      label {
        @include fig(400, 20, 80%, normal);
        font-size: 20px !important;
        color:rgba(0, 0, 1, 0.64);
      }

      .gchoice {
        margin-bottom: 20px;
      }

      input[type="checkbox"] {
        appearance: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        position: relative;
        width: 20px;
        height: 20px;
        margin-bottom: -4px;
      }

      input[type="checkbox"]::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 35%;
        border: 4px solid #babfc9;
      }

      input[type="checkbox"]:checked::before {
        border-color: #7A50CE;
      }

      input[type="checkbox"]::after {
        content: "";
        position: absolute;
        top: 5px;
        left: 5px;
        width: 10px;
        height: 10px;
        border-radius: 35%;
        background-color: #fff;
      }

      input[type="checkbox"]:checked::after {
        background-color: #7A50CE;
      }
    }

    .gfield--type-choice {

      .gfield_label {
        display: block;
        @include pop(400, 15, 100%, normal);
        color: #000;
        margin-bottom: 8px;

        @media (min-width: $screen-md) {
          @include pop(400, 20, 136%, normal);
          color: #000;
        }

      }

    }

    .frm_message, .gform_confirmation_wrapper {
      text-align: center;
      width: 100%;

      @media (min-width: $screen-md) {
        text-align: left;
      }

      h2 {
        margin-bottom: 20px;
        margin-top: 20px;
      }


      p {
        @include pop(400, 20, 100%, normal);
        letter-spacing: -0.35px;
        margin-bottom: 9px;
        color: #635E6E;
      }

      .file-download-button {
        display: flex;
        width: 205px;
        height: 65px;
        padding: 0px 12px 0px 50px;
        align-items: flex-start;
        gap: 4px;
        flex-direction: column;
        @include pop(400, 15, 100%, normal);
        letter-spacing: 0.15px;
        color: #5A6272;
        position: relative;
        border-radius: 6px;
        border: 1.2px solid #5A6272;
        justify-content: center;
        text-decoration: none;
        margin: 0 auto;
        margin-top: 40px;
        text-align: left;

        @media (min-width: $screen-md) {
          text-align: left;
          margin: 40px 0;
        }

        span {
          font-size: 10px;
        }

        &:before {
          content: url("/wp-content/themes/acro/images/svg/dark-pdf.svg");
          position: absolute;
          left: 5px;
          top:12px;
        }

      }

    }

  }

  .ebook_content_section {
    width: 100%;
    max-width: 100%;
    background: linear-gradient(180deg, #E9ECF2 0%, #E7E3ED 100%, #E8EAED 100%);
    padding: 40px 24px 0 24px;
    img {
      max-width: 100%;
    }
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    &__content {
      width: 100%;
      h2 {
        @media (min-width: 2500px) {
          font-size: 90px !important;
        }
      }
      @media (min-width: 2500px) {
        font-size: 36px !important;
      }
      p {
        @media (min-width: 2500px) {
          font-size: 36px !important;
        }
      }

    }
    &__image {
      margin: 0 auto;
    }

    @media (min-width: $screen-md) {
      max-width: 56%;
      padding: 72px 72px 0 72px;
    }

    h2 {
      @include pop(400, 29, 124%, normal);
      letter-spacing: -0.88px;
      margin-bottom: 32px;
      color: #000;

      @media (min-width: $screen-md) {
        @include pop(400, 44, 112%, normal);
      }

    }

    p {
      @include pop(400, 20, 156%, normal);
      letter-spacing: -0.35px;
      margin-bottom: 32px;
      color: rgba(0, 0, 1, 0.64);

      @media (min-width: $screen-md) {
        @include pop(400, 23, 156%, normal);
      }

    }

    .mobile_section_image {

      @media (min-width: $screen-md) {
        display: none;
      }

    }

    .desktop_section_image {
      display: none;

      @media (min-width: $screen-md) {
        display: block;
        object-fit: cover;
      }

    }


  }

  .content_section {
    width: 100%;
    max-width: 100%;
    background: linear-gradient(180deg, #E9ECF2 0%, #E7E3ED 100%, #E8EAED 100%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 32px;

    @media (min-width: $screen-md) {
      max-width: 56%;
      padding: 0 225px 0 72px;
    }

    h2 {
      @include pop(400, 23, 156%, normal);
      letter-spacing: -0.35px;
      margin-bottom: 32px;
      color: #000;

      @media (min-width: $screen-md) {
        @include pop(400, 35, 156%, normal);
      }

    }

    p {
      @include pop(400, 17, 156%, normal);
      letter-spacing: -0.35px;
      margin-bottom: 9px;
      color: rgba(0, 0, 1, 0.64);

      @media (min-width: $screen-md) {
        @include pop(400, 23, 156%, normal);
      }

    }

    ul {

      li {
        @include pop(400, 17, 156%, normal);
        letter-spacing: -0.35px;
        color: rgba(0, 0, 1, 0.64);
        padding: 12px 0 12px 50px;
        position: relative;
        @media (min-width: $screen-md) {
          @include pop(400, 23, 156%, normal);
        }


        &:before {
          content: url("/wp-content/themes/acro/images/white-check.png");
          position: absolute;
          left: 0;
          top: 16px;
        }

      }

    }

    .mobile_section_image {
      margin-bottom: 32px;

      @media (min-width: $screen-md) {
        display: none;
      }

    }

    .desktop_section_image {
      display: none;

      @media (min-width: $screen-md) {
        position: absolute;
        display: block;
        max-width: 230px;
        right: 0;
      }

    }

  }

  .gform_validation_errors {
    display: none;
  }

  .ssa_booking_iframe {
    min-height: 250px;
    max-height:500px !important;
  }

}

