.sdk_intro_container {
  background: linear-gradient(180deg, #F2F4F8 0%, #E7E3ED 100%, #F4F4F6 100%);

  .top_intro {
    padding-top: 135px;

      @media (min-width:$screen-md) {
        padding-top: 125px;
      }

    h2 {
      @include fig(400,20,156%,normal);
      text-align: center;
      color: #5A6272;
      margin-bottom: 16px;

      @media (min-width:$screen-md) {
        br {
          display: none;
        }
      }
    }

    h1 {
      @include pop(400,35,124%,normal);
      letter-spacing: -0.02em;
      background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      margin-bottom: 24px;
      text-align: center;
      padding: 0 20px;

      br {
        display: none;
      }

      @media (min-width:$screen-md) {
        @include pop(400,53,124%,normal);

        br {
          display: block;
        }

      }

    }

    p {
      @include fig(400,17,124%,normal);
      text-align: center;
      color: #4F4B58;
      padding: 0 20px;

      br {
        display: none;
      }

      @media (min-width:$screen-md) {
        @include fig(400,20,124%,normal);

        br {
          display: block;
        }

      }

    }


    .intro_image {
      position: relative;
      transform: scale(2.8);
      z-index: 0;
      margin-top: 55px;
      width: 100%;
      height: auto;

      @media (min-width:$screen-md) {
        top: -377px;
        margin-bottom: -930px;
        transform: scale(1);
        margin-top: unset;
      }
    }

  }

  .sdk_info_block_container {
    background: none;
  }

  .cta_section {
    padding-bottom: 66px;
    padding-top: 135px;

    @media (min-width:$screen-md) {
      padding-bottom: 135px;
      padding-top: 0px;
    }

    h2 {
      @include pop(400,35,124%,normal);
      letter-spacing: -0.7px;
      background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      margin-bottom: 40px;
      text-align: center;
      padding: 0 20px;

      @media (min-width:$screen-md) {
        @include pop(400,44,124%,normal);
      }

    }

    a {
      @include pop(500, 15, 100%, normal);
      padding: 0 16px;
      width: 70%;
      height: 64px;
      background: #27b989;
      box-shadow: 0px 6px 20px rgba(39, 168, 125, 0.15),
      0px 4px 8px rgba(39, 168, 125, 0.2025),
      0px 1px 3px rgba(39, 168, 125, 0.4);
      border-radius: 6px;
      display: flex;
      align-items: center;
      justify-content: center;
      letter-spacing: 0.01em;
      color: #ffffff;
      text-decoration: none;
      transition: 0.4s;
      margin: 0 auto;

      &:hover {
        background: #24a87d;
      }

      @media (min-width: $screen-md) {
        max-width: 240px;
      }
    }

  }

}