.softphone_linkup_component {
  background: #eeeff2;

  .container {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    padding: 20px;

    @media (min-width: $screen-md) {
      flex-direction: row;
      align-items: center;
      padding: 100px 80px 156px 80px;
      overflow: visible;
    }

    .content_column {
      order: 1;

      @media (min-width: $screen-md) {
        order: 0;
        flex-basis: 35%;
      }

      h2 {
        @include pop(400, 44, 112%, normal);
        letter-spacing: -0.02em;
        background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        margin-bottom: 24px;
        max-width: 92%;

        @media (min-width: $screen-md) {
          max-width: 100%;
        }
      }

      p {
        @include fig(400, 20, 136%, normal);
        color: #4f4b58;
        margin-bottom: 35px;
      }

      .softphone_linkup_component_icons {
        display: flex;
        flex-direction: row;
        gap: 26px;
        margin-bottom: 35px;

        @media (min-width: $screen-md) {
          gap: 12px;
          img {
            max-width: 60px;
          }
        }
      }

      .buttons {
        display: flex;
        flex-direction: column;
        gap: 16px;

        @media (min-width: $screen-md) {
          flex-direction: row;
        }

        .cta_button {
          @include pop(500, 15, 100%, normal);
          padding: 0 16px;
          width: 100%;
          height: 64px;
          background: #27b989;
          box-shadow: 0px 6px 20px rgba(39, 168, 125, 0.15),
            0px 4px 8px rgba(39, 168, 125, 0.2025),
            0px 1px 3px rgba(39, 168, 125, 0.4);
          border-radius: 6px;
          display: flex;
          align-items: center;
          justify-content: center;
          letter-spacing: 0.01em;
          color: #ffffff;
          text-decoration: none;
          transition: 0.4s;

          &:hover {
            background: #24a87d;
          }

          @media (min-width: $screen-md) {
            max-width: 240px;
          }
        }

        .video_button {
          @include pop(500, 15, 100%, normal);
          padding: 0 16px 0 50px;
          width: 100%;
          height: 64px;
          background: #fff;
          box-shadow: 0px 6px 20px rgba(104, 104, 104, 0.08),
            0px 4px 8px rgba(104, 104, 104, 0.12),
            0px 1px 3px rgba(104, 104, 104, 0.12);
          border-radius: 6px;
          display: flex;
          align-items: center;
          justify-content: center;
          letter-spacing: 0.01em;
          color: #000;
          text-decoration: none;
          position: relative;
          max-width: unset;
          cursor: pointer;

          @media (min-width: $screen-md) {
            max-width: 170px;
          }

          svg {
            max-width: 12px;
            position: absolute;
            left: 50px;

            @media (min-width: $screen-md) {
              left: 22px;
            }
          }
        }
      }
    }

    .image_column {
      order: 0;
      height: 580px;
      display: flex;
      align-items: center;
      justify-content: center;

      @media (min-width: $screen-md) {
        order: 1;
        flex-basis: 65%;
      }

      .mobile_image {
        transform: scale(1.8);
        display: block;
        width: 100%;
        height: auto;

        @media (min-width: $screen-sm) {
          transform: scale(1);
        }

        @media (min-width: $screen-md) {
          transform: scale(1);
          display: none;
        }
      }

      .desktop_image_layers {
        display: none;
        position: relative;

        @media (min-width: $screen-md) {
          display: block;
          width: 100%;
          height: 100%;
        }

        img {
          border: 0;
          width: 100%;
          height: auto;
          display: block;
        }

        .img_layer {
          position: absolute;
          transition: ease all 0.3s;
        }

        .layer_1 {
          z-index: 1;
        }
        .layer_2 {
          z-index: 2;
        }
        .layer_3 {
          z-index: 3;
        }

        .layer_5 {
          z-index: 5;
        }

        // img {
        //   width: auto;
        //   max-width: 100%;
        // }

        // .layer_5 {
        //   position: absolute;
        //   z-index: 15;
        //   left: 18%;
        //   top: -4%;
        //   transition: 0.4s;
        // }

        // .layer_4 {
        //   position: absolute;
        //   z-index: 14;
        //   left: 67.05%;
        //   top: 5.88%;
        // }

        // .layer_3 {
        //   position: absolute;
        //   z-index: 13;
        //   left: 19.33%;
        //   top: 18.75%;
        // }

        // .layer_2 {
        //   position: absolute;
        //   z-index: 12;
        //   left: -.37%;
        //   top: 29.21%;
        // }

        // .layer_1 {
        //   position: absolute;
        //   z-index: 11;
        //   left: 0;
        //   top: 32.28%;
        // }
      }
    }
  }
}

.video_popup {
  background: rgba(13, 11, 15, 0.4);
  backdrop-filter: blur(60px);
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  transition: 0.4s;
  padding: 25% 10%;
  z-index: 99999;

  @media (min-width: $screen-md) {
    padding: 5% 10%;
  }

  iframe {
    width: 100%;
    height: 100%;
  }

  .close-video {
    position: absolute;
    width: 35px;
    top: 2.5%;
    right: 5%;
    cursor: pointer;

    path {
      fill:#fff;
    }

  }

}