.partner_section_container {
  background: #f1f3f7;
  padding: 80px 0 60px 0;

  h3 {
    @include pop(400, 23, 156%, normal);
    color: #5a6272;
    text-align: center;
    margin-bottom: 40px;

    br {
      display: block;
    }

    @media (min-width: $screen-sm) {
      @include pop(400, 23, 136%, normal);

      br {
        display: none;
      }
      
    }


  }

  .partner_logos {
    display: flex;
    flex-wrap: wrap;
    padding:0 20px;

    @media (min-width: $screen-sm) {
      padding:0 40px;
      flex-wrap: nowrap;
      max-width: 1680px;
      margin: 0 auto;
      justify-content: space-between;
    }
  }

  .logo {
    max-width: 50%;
    display: flex;
    align-items: center;
    margin-bottom: 40px;
    justify-content: flex-start;
    flex-grow: 1;
    width: 100%;

    @media (min-width: $screen-sm) {
      justify-content: center;
      width: unset;
    }

    img {
      width: auto;
      max-height: 28px;
      max-width: 80%;
      @media (min-width: $screen-sm) {
        max-width: 100%;
      }
    }
  }
}
