.blog_slider_container {
  padding: 105px 20px 75px 20px;
  overflow: hidden;
  background: #EEEFF2;

  @media (min-width:$screen-md) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 110px 80px;
    position: relative;
    overflow: visible;
  }

  .inner_container {
    position: relative;
    z-index: 9;

    .slider_nav_desktop {
      display: none;
      position: absolute;
      top: -20px;
      right: 6.4%;

      @media (min-width:$screen-md) {
        display: flex;
      }

    }

    .slider_nav_mobile {
      display: flex;

      @media (min-width:$screen-md) {
        display: none;
      }

    }

    .swiper-slide {
      height: auto;

      .blog_post {
        background: #fff;
        height: 100%;
      }

    }

  }

  h2 {
    @include pop(400,35,124%,normal);
    margin-bottom: 30px;
    text-align: center;
    letter-spacing: -0.02em;
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    @media (min-width:$screen-md) {
      @include pop(400,44,120%,normal);
    }
  }

  a {
    text-decoration: none;
  }

  .blog_slider {

    .blog_post {
      box-shadow: 0px 100px 80px rgba(122, 124, 126, 0.03),
      0px 41.7776px 33.4221px rgba(122, 124, 126, 0.03),
      0px 22.3363px 17.869px rgba(122, 124, 126, 0.03),
      0px 12.5216px 10.0172px rgba(122, 124, 126, 0.03),
      0px 6.6501px 5.32008px rgba(122, 124, 126, 0.03),
      0px 2.76726px 2.21381px rgba(122, 124, 126, 0.03);

      .blog_slide_image {
        width: 100%;
        height: 231px;
        object-fit: cover;
      }

      .blog_slide_content {
        padding: 24px 32px;
        background: #FFFFFF;
        min-height: 63px;

        h3 {
          @include fig(400,20,156%,normal);
          color: #5A6272;
          text-decoration: none;
        }

      }

    }

  }

  .slider_navigation {
    margin-top: 17px;
    display: flex;
    justify-content: center;
    gap: 16px;

    .slide_button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 48px;
      background: #FFFFFF;
      box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1), 0px 24px 60px rgba(6, 47, 125, 0.05), 0px 12px 24px rgba(27, 59, 119, 0.05);
      border-radius: 12px;
      transition: 0.4s;
      cursor: pointer;

      svg {
        max-width: 8px;
      }

    }

    .next_slide {
      svg {
        transform: rotate(180deg);
      }
    }

    .swiper-button-disabled {
      opacity: 0.2;
    }

  }

}