.faq {
  padding: 24px 0;
  background: #f1f3f7;
  @media (min-width: $screen-md) {
    padding-bottom: 43px;
  }

  h2 {
    @include pop(400, 35, 124%, normal);
    letter-spacing: -0.02em;
    background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    margin-bottom: 40px;
    text-align: center;

    @media (min-width: $screen-md) {
      @include pop(400, 44, 112%, normal);
      margin-top: 40px;
    }
  }

  .container {
    @media (min-width: $screen-md) {
      max-width: 1680px;
      padding: 0 40px;
    }
  }
  .faq_row {
    background: #fff;
    margin-bottom: 8px;
    &.active {
      .icon {
        transform: rotate(45deg);
      }

      .title {
        @include titleGrad();
      }

      .faq_body {
        opacity: 1;
        visibility: visible;
        height: auto;
        padding: 24px;
      }
    }
  }
  .faq_title {
    display: flex;
    padding: 24px;
    color: #6436bf;
    @include pop(400, 23, 31px);
    letter-spacing: -0.02em;
    transition: 0.4s;
    box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1),
      0px 24px 60px rgba(6, 47, 125, 0.05),
      0px 12px 24px rgba(27, 59, 119, 0.05);

    &:hover {
      cursor: pointer;
    }

    .title {
      width: 100%;
      max-width: calc(100% - 24px);
    }

    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 16px;
      height: 16px;
      margin-left: auto;
      transition: ease all 0.4s;
      margin-top: 8px;
    }
  }

  .faq_body {
    @include fig(400, 18, 28px);
    color: #4f4b58;

    transition: ease all 0.4s;
    opacity: 0;
    visibility: hidden;
    height: 0;
    padding: 0 24px;
  }

  .btn_group {
    a {
      display: flex;
      width: 100%;
      align-items: center;
      padding: 32px 24px;
      color: #6436bf;
      @include pop(400, 23, 23px);
      text-decoration: none;

      .icon {
        margin-left: auto;

        svg {
          max-height: 17px !important;
        }

      }
    }
  }
}

.full_faq {
  .container {
    @media (min-width: 1680px) {
      max-width: 1680px !important;
      padding: 0 !important;
    }

    @media (min-width: 1440px) {
      max-width: 1680px !important;
      padding: 40px;
    }
  }
}

