.two_column_feature_component {
  display: flex;
  padding: 0px;
  gap: 20px;
  flex-direction: column;

  @media (min-width: $screen-md) {
    padding: 80px 70px;
    gap: 80px;
    flex-direction: row;
  }

  .col {
    flex: 1;
  }

  .image_column {
    order: 0;
    display: flex;
    justify-content: flex-end;

    @media (min-width: $screen-md) {
      order: 1;
    }

    img {
      max-width: 500px;
      margin-bottom: 40px;

      @media (min-width: $screen-md) {
        margin-bottom: 0;
      }

    }

  }

  .content_column {
    order: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin-bottom: 50px;

    @media (min-width: $screen-md) {
      order: 0;
      text-align: unset;
      margin-bottom: 0px;
    }

    h2 {
      @include pop(400, 29, 124%, normal);
      letter-spacing: -0.7px;
      background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      margin-bottom: 12px;

      @media (min-width: $screen-md) {
        @include pop(400, 29, 124%, normal);
        letter-spacing: -1.06px;
      }

    }

    .content {
      @include fig(400, 23, 156%, normal);
      color: #4F4B58;
    }

  }

  &.image_left {

    .image_column{
      order: 0;
      justify-content: flex-start;
    }

    .content_column {
      order: 1;
    }

  }



}