.features_section {
  padding: 120px 20px 80px 20px;
  overflow: hidden;
  background: #EEEFF2;
  

  @media (min-width:$screen-md) {
    background:linear-gradient(0deg, rgba(255, 255, 255, 1) 43%, rgba(238, 239, 242, 1) 100%);
  }

  .container {
    padding: 0;
    @media (min-width:$screen-md) {
      padding: 0;
    }
  }

  @media (min-width:$screen-md) {
    padding: 110px 80px 120px 80px;
  }

  h2 {
    @include pop(400,29,124%,normal);
    text-align: center;
    letter-spacing: -0.02em;
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    max-width: 90%;
    margin: 0 auto 30px auto;

    @media (min-width:$screen-md) {
      max-width: 100%;
      @include pop(400,44,124%,normal);
    }

  }

  .active_content_tab {
    display: none !important;
    @media (min-width:$screen-md) {
      display: flex !important;
    }
  }

  .feature_list {
    display: none;

    @media (min-width:$screen-md) {
      flex-direction: row;
      gap: 5px;
      flex-wrap: wrap;
    }


    .feature {
      height: 120px;
      background: #FFFFFF;
      padding: 16px 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      flex-grow: 1;
      flex-basis: 24%;
      box-shadow: 0 2px 4px rgba(7, 4, 146, .1), 0 24px 60px rgba(6, 47, 125, .05), 0 12px 24px rgba(27, 59, 119, .05);

      h3 {
        @include pop(400,18,124%,normal);
        color: #4F4B58;
      }

      img {
        max-width: 40px;
      }
    }

  }

  .feature_list_mobile {
    display: block;
    //filter: drop-shadow(0px 2px 4px rgba(7, 4, 146, 0.1)) drop-shadow(0px 24px 60px rgba(6, 47, 125, 0.05)) drop-shadow(0px 12px 24px rgba(27, 59, 119, 0.05));

    @media (min-width:$screen-md) {
      display: none;
    }

    .active_content_tab {
      display: block !important;
      @media (min-width:$screen-md) {
        display: flex !important;
      }
    }

    .feature_item {
      height: 136px;
      background: #FFFFFF;
      padding: 16px 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      box-shadow: 0 2px 4px rgba(7, 4, 146, .1), 0 24px 60px rgba(6, 47, 125, .05), 0 12px 24px rgba(27, 59, 119, .05);

      h3 {
        @include pop(400,15,124%,normal);
        color: #4F4B58;
      }

      img {
        max-width: 48px;
      }

    }

  }

  .feature_block_tabs {
    display: flex;
    margin-bottom: 30px;

    @media (min-width: $screen-md) {
      min-width: unset;
      justify-content: center;
    }

    ul {
      display: flex;
      flex-direction: row;

      li {
        max-width: 50%;

        @media (min-width: $screen-md) {
          max-width: unset;
        }

        @include pop(500, 15, 100%, normal);
        padding: 13px 24px;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: #6436bf;
        box-shadow: 0px 100px 80px rgba(122, 124, 126, 0.03),
        0px 41.7776px 33.4221px rgba(122, 124, 126, 0.03),
        0px 22.3363px 17.869px rgba(122, 124, 126, 0.03),
        0px 12.5216px 10.0172px rgba(122, 124, 126, 0.03),
        0px 6.6501px 5.32008px rgba(122, 124, 126, 0.03),
        0px 2.76726px 2.21381px rgba(122, 124, 126, 0.03);
        transition: 0.4s;
        background: #fff;
        cursor: pointer;
        text-align: center;

        @media (min-width: $screen-md) {
          padding: 20.5px 38px;
        }

        &:first-of-type {
          border-top-left-radius: 10px;
          border-bottom-left-radius: 10px;
        }

        &:last-of-type {
          border-top-right-radius: 10px;
          border-bottom-right-radius: 10px;
        }
      }

      .active {
        color: #fff;
        background: #6436bf;
        box-shadow: 0px 20px 51px rgba(83, 23, 213, 0.0975),
        0px 10.125px 22.2328px rgba(83, 23, 213, 0.15),
        0px 4px 8.2875px rgba(83, 23, 213, 0.2025),
        0px 0.875px 2.94844px rgba(83, 23, 213, 0.3);
      }
    }
  }


}
