.contained_cta_banner {
  background: #42247f;
  padding: 64px 0 0;
  @media (min-width: $screen-md) {
    padding: 0px 0 0;
    background: #eeeff2;
  }

  .container {
    @media (min-width: $screen-md) {
      background: #42247f;
      padding: 90px 0 0;
    }
  }

  .small_title {
    @include pop(400, 18, 124%);
    color: #cfbfed;
    text-align: center;
    margin-bottom: 16px;
    @media (min-width: $screen-md) {
      font-size: 23px;
      line-height: 136%;
      letter-spacing: -0.46px;
      margin-bottom: 18px;
    }

    br {
      @media (min-width: $screen-md) {
        display: none;
      }
    }
  }

  .big_title {
    @include pop(400, 29, 124%);
    letter-spacing: -0.58px;
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.25) 0%,
      #ffffff 0.01%,
      rgba(255, 255, 255, 0.54) 100%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-align: center;
    margin-bottom: 38px;

    @media (min-width: $screen-md) {
      font-size: 53px;
      letter-spacing: -1.06px;
      margin-bottom: 29px;
    }
  }

  .btn_group {
    justify-content: center;
    margin-bottom: 42px;
    @media (min-width: $screen-md) {
      margin-bottom: 57px;
    }
  }

  .image {
    @media (min-width: $screen-md) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    img {
      width: 100%;
      height: auto;
      @media (min-width: $screen-md) {
        max-width: 70%;
      }
    }
  }
}
