.demo_on_demand_banner {
  background: #321b5f;
  padding: 120px 0 80px;
  @media (min-width: $screen-md) {
    padding: 168px 0 0;
  }

  .background-image {
    width: 100%;
    position: relative;
    left: -20px;

    @media (min-width: 1023px) {
      margin-left: -100px;
      position: absolute;
      top: 0;
      left: 0;
      width: 50%;
      z-index: 1;
    }

    @media (min-width: $screen-mlg) {
      margin-left: 0;
    }

    img {
      width: 150%;
      margin-left: -13%;
      @media (min-width: 1023px) {
        width: 110%;
        margin-left: 0%;
      }
    }
  }

  .row_1 {
    margin-bottom: 32px;
    display: flex;
    flex-direction: column;

    @media (min-width: 1023px) {
      width: 100%;
      max-width: 644px;
      margin-left: auto;
      padding-right: 0px;
      z-index: 2;
      position: relative;
    }
    @media (min-width: $screen-mlg) {
      margin-right: 100px;
      max-width: 744px;
    }

    .title {
      @include pop(400, 29, 124%);
      letter-spacing: -0.58px;
      text-align: center;
      background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.25) 0%,
        #ffffff 0.01%,
        rgba(255, 255, 255, 0.54) 100%
      );
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      margin-bottom: 16px;
      order: 1;
      @media (min-width: $screen-sm) {
        text-align: left;
        font-size: 44px;
        line-height: 112%;
        letter-spacing: -0.88px;
      }

      br {
        @media (min-width: $screen-sm) {
          display: none;
        }
      }
    }

    .desc {
      @include fig(normal, 20, 124%);
      color: rgba(255, 255, 255, 0.8);
      text-align: center;
      margin-bottom: 16px;
      order: 2;
      @media (min-width: $screen-sm) {
        font-size: 23px;
        line-height: 124%;
        text-align: left;
        margin-bottom: 32px;

        br {
          display: none;
        }
      }
    }

    .form_subscribe_wrapper {
      margin-top: 25px;
      order: 4;
      border-radius: 10px;
      background: #fff;
      box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05),
        0px 24px 60px 0px rgba(6, 47, 125, 0.05),
        0px 2px 4px 0px rgba(7, 4, 146, 0.1);
      padding: 6px;
      @media (min-width: $screen-sm) {
        order: 3;
        margin-top: 0;
        margin-bottom: 32px;
      }

      form {
        display: flex;
        flex-wrap: wrap;
      }
    }

    .hs_email {
      width: 100%;
      @media (min-width: $screen-sm) {
        max-width: 60%;
      }
      label {
        display: none;
      }

      input {
        width: 100%;
        background: none;
        border: none;
        outline: none;
        margin: 0;
        padding: 19px 10px;
        box-shadow: none;
        @include fig(400, 18, 100%);
        color: #8c8698;
        text-align: center;
        @media (min-width: $screen-sm) {
          text-align: left;
          padding-left: 16px;
        }
      }
    }

    .hs_submit {
      width: 100%;
      @media (min-width: $screen-sm) {
        max-width: 40%;
      }

      .actions {
        height: 100%;
      }
      input {
        width: 100%;
        background: none;
        border: none;
        outline: none;
        margin: 0;
        padding: 19px 10px;
        box-shadow: none;
        border-radius: 6px;
        background: var(--secondary-400, #27b989);
        /* shadow green button */
        box-shadow: 0px 1px 3px 0px rgba(39, 168, 125, 0.4),
          0px 4px 8px 0px rgba(39, 168, 125, 0.2),
          0px 6px 20px 0px rgba(39, 168, 125, 0.15);
        @include pop(500, 16, 100%);
        letter-spacing: 0.16px;
        color: #fff;
        height: 100%;
      }
    }

    .icon_image {
      order: 3;

      @media (min-width: $screen-sm) {
        max-width: 301px;
        order: 4;
      }

      img {
        width: 100%;
        height: auto;
      }

    }
  }

  .row_2 {
    @media (min-width: $screen-sm) {
      position: relative;
      z-index: 3;
      width: 100%;
      max-width: 1190px;
      margin: 150px auto 0;
    }
    @media (min-width: $screen-md) {
      margin: 250px auto 0;
    }
    @media (min-width: $screen-lg) {
      margin: 350px auto 0;
    }
    .steps_mobile_timeline_image {
      width: calc(100% + 40px);
      margin-left: -20px;
      margin-bottom: 12px;
      @media (min-width: $screen-sm) {
        display: none;
      }

      img {
        width: 100%;
        height: auto;
      }

    }

    .steps_wrapper {
      @media (min-width: $screen-sm) {
        display: flex;
        justify-content: space-between;
        position: relative;
      }
    }

    .desktop_gragh {
      display: none;
      @media (min-width: $screen-sm) {
        position: absolute;
        display: block;
        z-index: 1;
        top: 0px;
        width: 120%;
        left: -10%;
      }
    }

    .step {
      display: flex;
      margin-bottom: 24px;
      @media (min-width: $screen-sm) {
        flex-direction: column;
        align-items: center;
        margin-bottom: 0;
        z-index: 3;
      }
    }

    .step1 {
      @media (min-width: $screen-sm) {
        margin-top: 96px;
      }
      @media (min-width: $screen-md) {
        margin-top: 160px;
      }
      .title {
        @media (min-width: $screen-sm) {
          order: 1;
        }
      }

      .description {
        @media (min-width: $screen-sm) {
          order: 2;
        }
      }

      .step_number {
        @media (min-width: $screen-sm) {
          order: 3;
          margin-bottom: 0;
          margin-top: 32px;
        }
      }
    }
    .step2 {
      @media (min-width: $screen-sm) {
        margin-top: 80px;
      }
      @media (min-width: $screen-md) {
        margin-top: 100px;
      }
    }
    .step3 {
      @media (min-width: $screen-sm) {
        margin-top: -15px;
      }
    }

    .step_number {
      width: 100%;
      max-width: 48px;
      height: 48px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #fff;
      border-radius: 100px;
      @include pop(400, 18, 124%);
      color: #7048c7;
      margin-right: 16px;
      @media (min-width: $screen-sm) {
        max-width: 72px;
        height: 72px;
        font-size: 29px;
        letter-spacing: -0.58px;
        margin-bottom: 32px;
      }
    }

    .step_title {
      @include pop(400, 23, 136%);
      letter-spacing: -0.46px;
      color: #fff;
      margin-bottom: 8px;
      @media (min-width: $screen-sm) {
        text-align: center;
        font-size: 23px;
        margin-bottom: 6px;
      }
    }

    .step_description {
      @include fig(normal, 15, 126%);
      color: rgba(255, 255, 255, 0.8);
      @media (min-width: $screen-sm) {
        max-width: 237px;
        text-align: center;
      }
    }
  }

  .row_3 {
    padding-top: 21px;
    margin-bottom: -140px;
    @media (min-width: $screen-sm) {
      margin-top: 46px;
      width: 100%;
      max-width: 1190px;
      margin: 46px auto 0px;
    }

    .row_3_slider_slider {
      width: 100%;
    }

    .swiper-slide {
      height: auto;
      background: #fff;
    }

    .slide_inner {
      padding: 24px 24px 39px;
      height: 100%;
      @media (min-width: $screen-sm) {
        padding: 24px 32px 60px;
        height: auto;
      }
    }

    .icon {
      width: 100%;
      max-width: 64px;
      margin-bottom: 12px;
    }

    .title {
      margin-bottom: 12px;
      @include pop(400, 18, 124%);
      @include titleGrad();
      @media (min-width: $screen-sm) {
        font-size: 23px;
        letter-spacing: -0.46px;
        line-height: 136%;
      }
    }

    .description {
      @include fig(normal, 16, 156%);
      color: #4f4b58;
      @media (min-width: $screen-sm) {
        font-size: 18px;
        max-width: 290px;
      }
    }
  }
}
.demo_on_demand_banner_spacer {
  height: 180px;
  width: 100%;
  display: flex;
  position: relative;

  background: #eeeff2;
}

.not_header_component {
  padding: 100px 0 16.5% !important;

  .row_1 {
    padding-top: 90px !important;
  }
}
