.glossary_page_content {
  background: #321B5F;


  .breadcrumb {
    padding: 112px 20px 56px 20px;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;

    @media (min-width: $screen-md) {
      padding: 112px 0 56px 0;
    }

    .button {
      @include pop(400, 35, 124%, normal);
      letter-spacing: -0.7px;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, #FFFFFF 0.01%, rgba(255, 255, 255, 0.6) 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      max-width: 400px;
      display: flex;
      flex-direction: row;
      gap: 12px;
      padding-right: 65px;

      img {
        max-width: 40px;
      }

    }

    .line {
      display: flex;
      width: 100%;
      align-items: center;

      .inner {
        background: #532D9F;
        height: 1px;
        width: 100%;

      }
    }

  }

  .top_section {
    padding: 0px 20px 50px 20px;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;

    @media (min-width: $screen-md) {
      padding: 0px 20px 90px 20px;
      flex-direction: row;
    }

    @media (min-width: $screen-mlg) {
      padding: 0px 0px 90px 0px;
    }


    .page_title {
      @include pop(400, 44, 112%, normal);
      letter-spacing: -1.06px;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, #FFFFFF 0.01%, rgba(255, 255, 255, 0.6) 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      max-width: 400px;
      margin-bottom: 52px;
      @media (min-width: $screen-md) {
        padding-right: 15px;
      }
    }

    .glossary_list {

      ul {
        display: flex;
        flex-direction: column;
        gap: 16px;

        li {
          padding: 12px 0;

          a {
            @include pop(400, 23, 136%, normal);
            letter-spacing: -1.06px;
            color: #fff;
            text-decoration: none;
          }

        }

      }

    }

  }

  &.simplified {
    .top_section {
      .page_title {
        max-width: 100%;
      }
    }
  }

}


.information_listing {
  background: #fff;
  max-width: 1440px;
  padding: 0px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  @media (min-width: $screen-md) {
    padding: 50px;
  }

  .information_block {
    background: #EEEFF2;
    margin-bottom: 4px;
    padding: 48px 32px;
    display: flex;
    flex-direction: column;
    gap: 24px;

    @media (min-width: $screen-md) {
      flex-direction: row;
      padding: 48px 80px;
      gap: 90px;
      margin-bottom: 1px;
    }

    .title {
      max-width: 100%;
      width: 100%;
      @include pop(400, 29, 124%, normal);
      letter-spacing: -1.06px;
      background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;

      @media (min-width: $screen-md) {
        max-width: 25%;
      }

    }

    .content {
      width: 100%;
      @include pop(400, 18, 156%, normal);
      color: #4F4B58;

      p {
        margin-bottom: 25px;
      }

      ul {
        padding-left: 40px;

        li{
          list-style: disc;
          margin-bottom: 16px;
        }
      }

    }

  }

}