.feature_intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;


  @media (min-width: $screen-md) {
    text-align: unset;
  }

  .icon {

    img {
      height: 80px;
      width: auto;
      margin-bottom: 16px;
    }

  }

  h1 {
    @include pop(400, 35, 124%, normal);
    letter-spacing: -0.7px;
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 5px;
    text-align: center;
    max-width: 100%;

    @media (min-width: $screen-md) {
      @include pop(400, 53, 124%, normal);
      letter-spacing: -1.06px;
      max-width: unset;
      margin-bottom: 16px;
    }

  }

  .content {
    @include fig(400, 20, 156%, normal);
    color: #4F4B58;
  }

  .large_intro_image_container {
    max-width: 820px;
  }

  .large_intro_image {
    width: 100%;

    @media (max-width: 768px) {
      margin-bottom: 20px;
    }

  }

  h2 {
    @include pop(400, 29, 124%, normal);
    letter-spacing: -0.7px;
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 12px;
    text-align: center;
    font-size: 23px;
    font-weight: 400;
    line-height: 31.28px;


    @media (min-width: $screen-md) {
      @include pop(400, 29, 124%, normal);
      letter-spacing: -1.06px;
    }

  }

  .sub_content {
    @include fig(400, 20, 156%, normal);
    color: #4F4B58;

  }

  .lower_image {
    max-width: 480px;
    margin: 30px auto 120px auto;

    @media (min-width: $screen-md) {
      margin: 30px auto;
    }

  }

}