.page_cta_banner, .page404 {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #fff;
  @media (min-width: $screen-md) {
    padding: 200px 0 50px;
    min-height: 50vh;
    // background-image: url(../../images/banner1.png);
    background-position: 0 -50px;
    background-repeat: no-repeat;
    background-size: 100% auto;
  }

  @media (min-width: $screen-lg) {
    padding: 250px 0 100px;
  }
  .container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .icon {
    width: 100%;
    max-width: 44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 24px;
    @media (min-width: $screen-md) {
      margin-bottom: 32px;
    }
  }

  h1 {
    @include pop(400, 35, 43px);
    letter-spacing: -0.02em;
    text-align: center;
    @include titleGrad();
    margin-bottom: 24px;
    @media (min-width: $screen-md) {
      font-size: 53px;
      line-height: 65px;
    }

    br {
      display: none;
      @media (min-width: $screen-md) {
        display: block;
      }
    }
  }

  .desc {
    margin-bottom: 48px;
    @media (min-width: $screen-md) {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 51px;
    }
    p {
      @include fig(500, 20, 24px);
      @include titleGrad();
      text-align: center;

      &:first-child {
        margin-bottom: 8px;
        @media (min-width: $screen-md) {
          margin-bottom: 0;
          margin-right: 8px;
        }
      }
    }

    img {
      width: 122px;
      margin: 0 auto;
      height: auto;
    }
  }
}

