.legacy_stepped_form {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 0;

  @media (min-width:$screen-md) {
    padding: 40px;
  }

  .container {
    padding:0;
  }

  .form_component {
    background: linear-gradient(180deg,#3a1f6e 0,#40237a 33.29%,#5d4292 96.88%)!important;
    border-radius: 24px!important;
    height: unset;

    @media (min-width:$screen-md) {
      height: 610px;
    }

    .form_tabs {
      padding: 0 10px;

      @media (min-width:$screen-md) {
        padding: 0 40px;
      }

      ul {
        display: flex;
        flex-direction: row;
        gap: 4.25px;

        li {
          height: 60px;
          position: relative;
          flex-grow: 1;
          flex-basis: 25%;


          &:before {
            display: block;
            content: '';
            width: 100%;
            height: 6px;
            top: 0;
            left: 0;
            border-radius: 0px 0px 8px 8px;
          }

          &:after {
            content: '';
            width: 16px;
            height: 16px;
            position: absolute;
            left: 0;
            top: 15px;
          }

          span {
            @include fig(400,13,250%,normal);
            line-height: 259%;
            letter-spacing: -0.51px;
            color: #fff;
            padding-left: 20px;

            @media (min-width:$screen-md) {
              letter-spacing: 0.01em;
              color: #fff;
              padding-left: 24px;
            }

          }

        }

        .inactive {

          &:before {
            background: #BABFC9;
          }

          &:after {
            content: url('/wp-content/themes/acro/images/svg/inactive.svg');
          }

        }

        .busy {
          &:before {
            background: #27B989;
          }

          &:after {
            content: url('/wp-content/themes/acro/images/svg/busy-legacy.svg');
          }
        }

        .done {

          &:before {
            background: #5A6272;
          }

          &:after {
            content: url('/wp-content/themes/acro/images/svg/done-legacy.svg');
          }
        }


      }


    }

    .form_content_blocks {
      display: flex;
      height: 100%;
      width: 100%;
      align-items: center;
      justify-content: center;
      top: -20px;
      position: relative;
      padding-top: 40px;

      @media (min-width:$screen-md) {
       padding-top: 0;
      }

      .md-input-container {
        border-radius: 6px;
        border: 1.2px solid var(--background-400, #A4AAB7);
        padding-top: 0 !important;

        i {
          display: none !important;
        }

        label {
          left: 16px !important;
          top: 15px !important;
        }

        input {
          height: 48px;
        }

      }

      .md-has-value {
        label {
          display: none !important;
        }
      }

      h2 {
        @include pop(400,35,124%,normal);
        color: #fff;
        margin-bottom: 16px;
        text-align: center;

        @media (min-width:$screen-md) {
          @include pop(400,35,100%,normal);
        }

      }

      h3 {
        @include fig(400,20,136%,normal);
        text-align: center;
        color: rgba(255,255,255,.64);
        margin-bottom: 40px;

        @media (min-width:$screen-md) {
          @include fig(400,23,100%,normal);
        }

      }

      .email_block {
        display: flex;
        flex-direction: column;
        padding: 0 20px;

        @media (min-width:$screen-md) {
          padding: 0;
        }

        .email_form {
          display: inline-block;
          background: #FFFFFF;
          box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1), 0px 24px 60px rgba(6, 47, 125, 0.05), 0px 12px 24px rgba(27, 59, 119, 0.05);
          border-radius: 10px;
          margin: 0 auto;
          padding: 4px;

          @media (min-width:$screen-md) {
            height: 64px;
            overflow: hidden;
          }

          input {
            @include fig(400,18,100%,normal);
            font-family: 'Figtree';
            font-style: normal;
            align-items: center;
            color: #000;
            border: none;
            min-width: 240px;
            height: 64px;
            text-align: center;
            width: 100%;

            @media (min-width:$screen-md) {
              height: 100%;
              margin-left: 20px;
              width:unset;
              text-align: left;
            }
          }

          button {
            @include pop(500,16,100%,normal);
            letter-spacing: 0.01em;
            color: #FFFFFF;
            padding: 0 16px;
            gap: 40px;

            height: 56px;
            background: #27B989;
            //box-shadow: 0px 6px 20px rgba(39, 168, 125, 0.15), 0px 4px 8px rgba(39, 168, 125, 0.2025), 0px 1px 3px rgba(39, 168, 125, 0.4);
            border-radius: 6px;
            cursor: pointer;
            transition: 0.4s;
            width: 100%;

            @media (min-width:$screen-md) {
              width: 218px;
            }

            &:hover {
              background: #24A87D;
            }
          }

        }

        .email_validation {
          @include pop(400,16,100%,normal);
          color: red;
          margin-top: 20px;
          text-align: center;
        }



      }

      .date_block {
        width: 100%;

        .overlay {
          position: absolute;
          width: 100%;
          height: calc(100% - 50px);
          display: flex;
          align-items: center;
          justify-content: center;
          top: 50px;
          left: 0;
          background: linear-gradient(180deg,#3d2172 0,#40237a 33.29%,#5d4292 96.88%);

          .spinner {
            animation: rotate 2s linear infinite;
            z-index: 2;
            position: absolute;
            top: 50%;
            left: 50%;
            margin: -25px 0 0 -25px;
            width: 50px;
            height: 50px;

            & .path {
              stroke: #fff;
              stroke-linecap: round;
              animation: dash 1.5s ease-in-out infinite;
            }

          }

          @keyframes rotate {
            100% {
              transform: rotate(360deg);
            }
          }

          @keyframes dash {
            0% {
              stroke-dasharray: 1, 150;
              stroke-dashoffset: 0;
            }
            50% {
              stroke-dasharray: 90, 150;
              stroke-dashoffset: -35;
            }
            100% {
              stroke-dasharray: 90, 150;
              stroke-dashoffset: -124;
            }
          }


        }

      }

      .personal_info_block {

      }

      .finish_block {

      }

    }

  }

}

.booking {
  background: red;
}
