.sdk_info_block_container {
  overflow: scroll;
  background: #eeeff2;
  padding-bottom: 80px;

  @media (min-width: $screen-md) {
    overflow: visible;
    padding: 120px 0;
    position: relative;
  }

  .container {
    z-index: 2;
  }

  .title_section {
    margin-bottom: 25px;

    @media (min-width: $screen-md) {
      margin-bottom: 40px;
    }

    h2 {
      @include pop(400, 35, 124%, normal);
      margin-bottom: 12px;
      text-align: center;
      letter-spacing: -0.02em;
      background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      @media (min-width: $screen-md) {
        @include pop(400, 44, 124%, normal);
      }
    }

    h3 {
      @include pop(400, 20, 156%, normal);
      color: #5a6272;
      text-align: center;
      margin-bottom: 14px;
    }

    .link {
      @include pop(400, 17, 124%, normal);
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #6436bf;
      text-decoration: none;
      display: block;
      margin: 0 auto;
      text-align: center;
    }
  }

  .sdk_blocks {
    .sdk_block_tabs {
      display: flex;
      justify-content: center;
      min-width: 700px;

      @media (min-width: $screen-md) {
        min-width: unset;
      }

      ul {
        display: flex;
        flex-direction: row;

        li {
          @include pop(500, 15, 100%, normal);
          padding: 13px 24px;
          letter-spacing: 0.02em;
          text-transform: uppercase;
          color: #6436bf;
          box-shadow: 0px 100px 80px rgba(122, 124, 126, 0.03),
            0px 41.7776px 33.4221px rgba(122, 124, 126, 0.03),
            0px 22.3363px 17.869px rgba(122, 124, 126, 0.03),
            0px 12.5216px 10.0172px rgba(122, 124, 126, 0.03),
            0px 6.6501px 5.32008px rgba(122, 124, 126, 0.03),
            0px 2.76726px 2.21381px rgba(122, 124, 126, 0.03);
          transition: 0.4s;
          background: #fff;
          cursor: pointer;
          text-align: center;

          @media (min-width: $screen-md) {
            padding: 20.5px 38px;
          }

          &:first-of-type {
            border-top-left-radius: 10px;
            border-bottom-left-radius: 10px;
          }

          &:last-of-type {
            border-top-right-radius: 10px;
            border-bottom-right-radius: 10px;
          }
        }

        .active {
          color: #fff;
          background: #6436bf;
          box-shadow: 0px 20px 51px rgba(83, 23, 213, 0.0975),
            0px 10.125px 22.2328px rgba(83, 23, 213, 0.15),
            0px 4px 8.2875px rgba(83, 23, 213, 0.2025),
            0px 0.875px 2.94844px rgba(83, 23, 213, 0.3);
        }
      }
    }

    .sdk_block_intros {
      margin: 20px 0 25px 0;

      ul {
        li {
          p {
            @include fig(400, 15, 156%, normal);
            font-size: 15px !important;
            text-align: center;
            color: #4f4b58;
          }
        }
      }
    }

    .sdk_block_codes {
      position: relative;

      @media (min-width: $screen-md) {
        max-width: 1140px;
        margin: 0 auto;
      }

      .os_switch {
        position: absolute;
        bottom: -65px;
        display: flex;
        width: 100%;
        justify-content: center;

        @media (min-width: $screen-md) {
          left: 0;
          top: 44%;
          bottom: unset;
        }

        ul {
          display: flex;
          flex-direction: row;
          gap: 10px;
          width: 100%;
          justify-content: center;

          @media (min-width: $screen-md) {
            flex-direction: column;
          }

          li {
            width: 32px;
            height: 32px;
            border: 1px solid rgba(0, 0, 0, 0.24);
            border-radius: 44px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            transition: 0.4s;

            svg {
              max-width: 14px;
            }
          }

          .active {
            background: #24272e;
            border-color: #24272e;

            svg {
              path {
                fill: #fff;
                fill-opacity: 1;
              }
            }
          }
        }
      }

      .code_block {
        max-width: 1000px;
        background: #1b1d22;
        margin: 0 auto;
        border-radius: 20px;
        height: 570px;
        overflow: hidden;
        min-width: 1000px;
        box-shadow: 0px 9px 38px rgba(0, 0, 0, 0.38),
          0px 3.46667px 12.1037px rgba(0, 0, 0, 0.230815),
          0px 0.733333px 3.0963px rgba(0, 0, 0, 0.149185);

        .code_block_header {
          height: 55px;
          display: flex;
          flex-direction: row;
          align-items: center;
          background: #24272e;

          .icons {
            min-width: 110px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 22px;

            svg {
              max-width: 72px;
            }

            span {
              display: block;
              width: 15px;
              height: 15px;
              border-radius: 50%;
              &.red {
                background: #f24b4b;
              }
              &.yellow {
                background: #f8b83e;
              }
              &.green {
                background: #48c542;
              }
            }
          }

          .title {
            background: #1b1d22;
            color: #fff;
            height: calc(100% - 2px);
            top: 1px;
            position: relative;
            padding: 0 22px;
            display: flex;
            align-items: center;
            white-space: nowrap;
            @include ibm(400, 15, 20px, normal);
          }

          .powered {
            width: 100%;
            padding: 0 22px;
            text-align: right;
            font-size: 11px;
            color: #fff;
            font-family: monospace;
            letter-spacing: -0.5px;

            svg {
              max-width: 224px;
            }
          }
        }

        .code_part {
          padding: 0px 0 30px 0;
          color: #ffffff;
          @include ibm(400, 17, 136%, normal);

          pre {
            margin: 0;
            padding: 30px;

            code {
              counter-increment: listing;
              margin-bottom: 5px;

              &:before {
                content: counter(listing) " ";
                display: inline-block;
                margin-right: 30px;
                height: 25px;
                opacity: 0.5;
              }
            }
          }

          .android {
            overflow: auto;
            max-height: 515px;
            min-height: 515px;
          }

          .ios {
            overflow: auto;
            min-height: 515px;
            max-height: 515px;
          }
        }
      }
    }
  }

  .soft_grad {
    position: absolute;
    top: -60%;
    z-index: 0;
    width: 200%;
    left: -65%;
  }
}
