.hubspot_form_section {
  padding: 125px 20px 0 20px;
  background: #f1f3f7;

  @media (min-width: $screen-md) {
    padding: 145px 40px 0 40px;
  }

    .selectric-wrapper {
      .selectric {
        width: 100%;
        height: 56px;
        padding: 7px 16px;
        border-radius: 4px;
        border: 1.2px solid #babfc9;
        margin-bottom: 32px !important;
        color: #b6b2bd;
        font-size: 18px;
        font-family: Figtree, sans-serif !important;
        line-height: 156%;
        margin-top: 5px;
        background: #fff;

        span {
          color: #b6b2bd;
          font-size: 18px;
          font-family: Figtree,sans-serif!important;
          line-height: 156%;
        }
      }
    }

  .intro {

    h1 {
      @include pop(400,53,124%,normal);
      text-align: center;
      letter-spacing: -0.02em;
      background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      margin-bottom: 16px;

      @media (min-width:$screen-md) {
        @include pop(400,65,100%,normal);
      }

    }

    h2 {
      @include pop(400,18,124%,normal);
      text-align: center;
      color: #4F4B58;
      margin-bottom: 22px;

      @media (min-width:$screen-md) {
        @include pop(400,18,100%,normal);
        margin-bottom: 45px;
      }
    }

  }

  .form_section {
    padding: 40px;
    max-width: 1680px;
    margin: 0 auto;
    background: #fff;
    box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);

    label {

      span {
        @include pop(400,18,124%,normal);
        color: #1E1C21;
      }

    }

    .hs-main-font-element {
      padding: 16px 24px;
      border-radius: 6px;
      background: #E7DFF6;


        p {
          &:first-of-type {
            display: none;
          }
        }


      span {
        font-family: "Figtree", sans-serif !important;
        font-weight: 400 !important;
        line-height: 136% !important;
        color: #4F4B58 !important;
        font-size: 15px !important;

        a {
          color:#7A50CE !important;
          text-decoration: underline !important;
        }

      }

      strong {
        font-weight: 400 !important;
      }

      margin-bottom: 36px;
    }

    .hs_TICKET\.csm_support_product {
      margin-bottom: 24px !important;
    }

    input[type='text'], input[type='email'],select,textarea {
      width: 100%;
      height: 56px;
      padding: 12px 16px;
      border-radius: 4px;
      border: 1.2px solid #BABFC9;
      margin-bottom: 32px !important;
      color: #B6B2BD;
      font-size: 18px;
      font-family: "Figtree", sans-serif !important;
      line-height: 156%;
      margin-top: 5px;

      &::placeholder {
        color: #B6B2BD;
        font-size: 18px;
        font-family: "Figtree", sans-serif !important;
        line-height: 186%;
        font-weight: 400 !important;
      }

    }

    .input {
      position: relative;
    }

    .input input[type="file"] {
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      height: 48px;
      width: 100%;
      cursor: pointer;
    }

    .hs_TICKET\.csm_ticket_file_upload .input {
      height: 48px;
      margin: 5px 0 45px 0;
    }

    .hs_TICKET\.csm_ticket_file_upload .input::before {
      padding: 0px 20px;
      height: 48px;
      content: 'Choose File';
      border-radius: 6px;
      border: 1.2px solid #A4AAB7;
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      color: #A4AAB7;
      font-size: 12px;
      display: inline-block;
      line-height: 45px;

      @media (min-width:$screen-md) {
        padding: 0px 240px;
      }

    }


    .hs_TICKET\.csm_ticket_file_upload .input::after {
      padding: 0px 10px;
      height: 48px;
      content: attr(data-content);
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      color: #A4AAB7;
      font-size: 15px;
      display: inline-block;
      line-height: 45px;
    }

    .hs-field-desc {
      color: #8C8698;
      font-size: 15px;
      font-family: "Figtree", sans-serif !important;
      line-height: 136%;
      margin-top: 5px;
    }

    .hs-error-msg {
      padding: 0 !important;
      background: none !important;
      color: red !important;
      position: relative;
      top: -28px;
    }

    .hs-error-msgs {

      label {
        padding: 0 !important;
        background: none !important;
        color: red !important;
      }

    }

    .actions {
      margin-top: 40px;

      .hs-button {
        height: 64px;
        border-radius: 10px;
        background: #6436BF;
        width: 300px;
        font-family: "Poppins", sans-serif;
        font-weight: 500;
        color: #FFF;
        font-size: 20px;
        border: none;
      }

    }

    input[type="radio"] {
      appearance: none;
      -webkit-appearance: none;
      -moz-appearance: none;
      position: relative;
      width: 20px;
      height: 20px;
      margin-bottom: -4px;
    }

    input[type="radio"]::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 2px solid #BABFC9;
    }

    input[type="radio"]:checked::before {
      border-color: #7A50CE;
    }

    input[type="radio"]::after {
      content: "";
      position: absolute;
      top: 5px;
      left: 5px;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: #fff;
    }

    input[type="radio"]:checked::after {
      background-color: #7A50CE;
    }

    .hs-fieldtype-radio .input {
      margin-top: 10px;
    }

    .hs-fieldtype-radio .input span{
      margin-left: 10px;
    }

    .hs-fieldtype-radio .input li{
      margin-bottom: 8px;
    }

    .hs-fieldtype-radio .hs-error-msg {
      top: unset;
    }


  }

}


