.bundle_section {
  background: #eeeff2;
  padding: 65px 0 65px 0 ;

  .container {
    padding: 0 20px;

    @media (min-width:$screen-md) {
      padding: 0 40px;
    }

  }

  .icon {
    max-width: 48px;
    margin: 0 auto;
    margin-bottom: 16px;
    display: block;
  }

  h2 {
    @include pop(400, 29, 124%, normal);
    letter-spacing: -0.7px;
    background:  linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    margin-bottom: 46px;
    text-align: center;

    @media (min-width: $screen-md) {
      @include pop(400, 35, 124%, normal);
      letter-spacing: -1.06px;
    }
  }

  .bundle_items_slider {

    .bundle_item {
      padding: 24px 32px 32px 32px;
      background: #FFF;
      box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
      position: relative;

      .number {
        position: relative;
        color:#7A50CE;
        @include pop(400, 44, 112%, normal);
        letter-spacing: -0.88px;
        margin-bottom: 32px;

        &:before {
          content: "";
          width: 54px;
          height: 4px;
          top: -25px;
          left: 0;
          background: #7a50ce;
          position: absolute;
        }

      }

      h4 {
        @include pop(400, 23, 136%, normal);
        letter-spacing: -0.46px;
        background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        margin-bottom: 16px;
      }

      .content {
        @include fig(400, 18, 136%, normal);
        color: #635E6E;
        margin-bottom: 16px;
      }

      a {
        @include pop(400, 17, 124%, normal);
        color: #6436BF;
        text-transform: uppercase;
        text-decoration: none;
      }

      &:before {
        content: '';
        width: 90%;
        height: 10px;
        position: absolute;
        bottom: -10px;
        background: #fff;
        left: 5%;
        background: rgba(255, 255, 255, 0.80);

        /* block-222 */
        box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
      }

      &:after {
        content: '';
        width: 80%;
        height: 10px;
        position: absolute;
        bottom: -20px;
        background: #fff;
        left: 10%;
        background: rgba(255, 255, 255, 0.80);
        box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
      }

    }



  }

}