.glossary_listing {
  padding: 122px 0 100px 0;
  background: #321B5F;

  .glossary_container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;

    @media (min-width: $screen-md) {
      padding: 0;
    }

    h1 {
      display: flex;
      flex-direction: row;
      gap: 12px;
      @include pop(400, 35, 124%, normal);
      letter-spacing: -1.06px;
      margin-bottom: 44px;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, #FFFFFF 0.01%, rgba(255, 255, 255, 0.6) 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;

      @media (min-width: $screen-md) {
        @include pop(400, 53, 132%, normal);
        gap: 16px;
        margin-bottom: 55px;
      }

      img {

        max-width: 48px;
        @media (min-width: $screen-md) {
          max-width: 64px;
        }

      }

    }

    .glossary_items {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;

      @media (min-width: $screen-md) {
        flex-direction: row;
      }

      .glossary_item {
        position: relative;
        min-width: 100%;
        max-width: 100%;
        display: flex;
        align-items: center;
        border: 1px solid #532D9F;
        padding: 20px 16px;
        @include fig(400, 23, 132%, normal);
        color: #fff;
        transition: 0.4s;
        cursor: pointer;
        text-decoration: none;
        gap: 16px;

        @media (min-width: $screen-md) {
          min-width: 25%;
          max-width: 25%;
          align-items: flex-end;
          max-height: 180px;
          min-height: 180px;
          gap: unset;
          padding: 24px;
        }

        .icon {
          min-width: 32px;
          max-width: 33px;

          @media (min-width: $screen-md) {
            position: absolute;
            top: 24px;
            left: 24px;
            min-width: unset;
          }
        }

        &:hover {
          background: #532D9F;
        }

      }

    }

  }

}