.csp-logorama {
  padding: 80px 0 56px;

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 112px;
    padding: 0 15px;
    background: #fff;
    box-shadow: 0 4.02054px 8.33614px rgba(122, 124, 126, 0.04), 0 1.19702px 1.63939px rgba(122, 124, 126, 0.04);
    border-radius: 12px;
    transition: all 0.2s ease-in-out !important;

    &:hover {
      box-shadow: 0 4.02054px 10.33614px rgba(122, 124, 126, 0.1), 0 1.19702px 3.63939px rgba(122, 124, 126, 0.1);
    }

    @media (min-width: 992px) and (max-width: 1199.98px) {
      height: 96px;
    }
  }

  @media (min-width: 992px) {
    &__item-wrp:nth-of-type(2) img {
      max-width: 90%;
    }

    &__item-wrp:nth-of-type(7) img {
      max-width: 80%;
    }
  }

  @media (max-width: 991.98px) {
    &__item-wrp:nth-of-type(1) img,
    &__item-wrp:nth-of-type(2) img {
      height: 16px !important;
      width: auto;
    }

    &__item-wrp:nth-of-type(5) img {
      height: 24px !important;
      width: auto;
    }

    &__item-wrp:nth-of-type(7) img {
      height: 20px !important;
      width: auto;
    }
  }
}
