.cloud_softphone_feature_videos {
  background: #eeeff2;
  padding-bottom: 64px;
  position: relative;
  @media (min-width: $screen-md) {
    padding-bottom: 96px;
  }

  .section_title {
    margin-bottom: 24px;
    @media (min-width: $screen-md) {
      margin-bottom: 32px;
    }
    .title {
      @include pop(400, 35, 43px);
      letter-spacing: -0.7px;
      @include titleGrad();
      text-align: center;
      @media (min-width: $screen-md) {
        font-size: 44px;
        line-height: 112%;
        letter-spacing: -0.88px;
      }
    }
  }

  .feature_video_wrapper {
    padding: 0 16px;
    margin: 0 auto;
    width: 100%;
    position: relative;
    max-width: 1190px;
  }

  .video_box {
    background: #fff;
  }

  .video_holder {
  }

  .img_wrapper {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #1c1232;
      opacity: 0.5600000023841858;
    }

    .icon {
      position: absolute;
    }

    img {
      object-fit: cover;
      height: 100%;
      width: 100%;
    }
  }

  .content_holder {
    padding: 24px;
    @media (min-width: $screen-md) {
      display: flex;
      flex-direction: column;
    }
  }

  .title {
    @include pop(400, 18, 124%);
    @include titleGrad();
    margin-bottom: 8px;
    @media (min-width: $screen-md) {
      font-size: 23px;
      letter-spacing: -0.46px;
      line-height: 136%;
      margin-bottom: 23px;
    }
  }

  .desc {
    @include fig(400, 16, 156%);
    overflow: hidden;
    color: #4f4b58;
    text-overflow: ellipsis;
    whitespace: nowrap;
    margin-bottom: 12px;
  }

  .video_meta {
    display: flex;
    align-items: center;
    @media (min-width: $screen-md) {
      margin-top: auto;
    }

    .tag {
      @include fig(600, 12, 156%);
      letter-spacing: 0.84px;
      color: #1e8f69;
      padding: 8px 20px;
      border: 1px solid #46d8a7;
      border-radius: 100px;
      text-transform: uppercase;
    }

    .length {
      display: flex;
      margin-left: 15px;
      align-items: center;
      justify-content: center;
      @include fig(600, 12, 156%);
      letter-spacing: 0.84px;
      color: #000;
      text-transform: uppercase;

      .star {
        width: 100%;
        max-width: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 15px;
      }
    }
  }

  .bottom_feature {
    @media (min-width: $screen-md) {
      display: flex;
      margin-left: -8px;
      margin-right: -8px;
    }
  }

  .small_video_box {
    display: flex;
    position: relative;
    margin-bottom: 16px;

    @media (min-width: $screen-md) {
      flex-direction: column;
      width: 100%;
      max-width: calc(33.333% - 16px);
      margin: 0 8px;
    }

    .video_holder {
      width: 100%;
      max-width: 99px;
      height: 100%;
      position: absolute;
      @media (min-width: $screen-md) {
        position: relative;
        max-width: 100%;
        order: 1 !important;
        max-height: 260px;
      }

      .img_wrapper {
        height: 100%;
        @media (min-width: $screen-md) {
        }

        img {
          height: 100%;
          object-fit: cover;
        }
      }
    }

    .content_holder {
      width: 100%;
      max-width: calc(100% - 99px);
      margin-left: 99px;
      @media (min-width: $screen-md) {
        margin-left: 0;
        max-width: 100% !important;
        order: 2 !important;
        height: 100%;
        padding: 24px 32px 32px !important;
      }
    }

    .video_meta {
      .tag {
        border: none;
        padding: 0;
        @media (min-width: $screen-md) {
          padding: 8px 20px;
          border: 1px solid #46d8a7;
        }
      }
    }
  }

  // Layout 1
  // Layout 2
  // Layout 3

  .layout_1 {
    .top_feature {
      margin-bottom: 16px;
    }

    .video_box {
      @media (min-width: $screen-md) {
        display: flex;
      }
    }

    .video_holder {
      @media (min-width: $screen-md) {
        order: 2;
        width: 100%;
      }
    }

    .content_holder {
      @media (min-width: $screen-md) {
        order: 1;
        width: 100%;
        max-width: 30%;
        padding: 32px;
      }
    }
  }

  .layout_2 {
    @media (min-width: $screen-mlg) {
      display: flex;
    }
    .left_col {
      margin-bottom: 16px;
      @media (min-width: $screen-mlg) {
        margin-bottom: 0;
        width: 100%;
        max-width: 75%;
      }

      .video_box {
        height: 100%;
      }

      .img_wrapper {
        @media (min-width: $screen-mlg) {
          height: 100%;
        }

        img {
          @media (min-width: $screen-mlg) {
            height: 100%;
          }
        }
      }
    }

    .right_col {
      @media (min-width: $screen-mlg) {
        width: 100%;
        max-width: 25%;
      }

      .small_video_box {
        @media (min-width: $screen-mlg) {
          max-width: 100%;
          height: 100%;
        }
      }
    }
  }

}

.csf_video_modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 99999;
  background: rgba(13, 11, 15, 0.4);
  backdrop-filter: blur(60px);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: ease all 0.3s;

  @media (min-width: $screen-md) {
    // height: 70vh;
  }

  &.active {
    opacity: 1;
    visibility: visible;
  }

  .watch_video_close {
    position: absolute;
    top: 24px;
    right: 24px;
    width: 100%;
    max-width: 20px;
    z-index: 90;
    @media (min-width: $screen-md) {
      top: 100px;
      right: 200px;
    }

    &:hover {
      cursor: pointer;
    }

    svg {
      path {
        fill: #fff;
      }
    }
  }
  .video_container {
    position: relative;
    width: 100%;
    padding: 20px;
    padding-bottom: 56.25%;
    @media (min-width: $screen-md) {
      padding: 100px 0;
      position: relative;
      padding-bottom: 56.25%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    iframe {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      @media (max-width: $screen-md) {
        left: 0;
      }
      @media (min-width: $screen-md) {
        width: 60%;
        height: 60%;
        top: 20%;
      }
    }

    &.has_video {
      @media (min-width: $screen-md) {
        padding-bottom: 0;
      }
    }

    video {
      width: 100%;
      height: auto;
      top: 0;
      @media (min-width: $screen-md) {
        width: 60%;
      }
    }
  }
}
