.white_label_container {
  background-size: cover;
  background-position: 50%;
  padding-top: 125px;
  text-align: center;

  .icon {
    max-width: 44px;
    margin: 0 auto 32px auto;
  }


  h1 {
    @include pop(400,44,120%,normal);
    text-align: center;
    letter-spacing: -0.02em;
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    margin-bottom: 20px;
  }

  h2 {
    @include fig(500,23,124%,normal);
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    text-align: center;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    margin-bottom: 40px;

    p {
      display: flex;
      flex-direction: column;
      align-items: center;

      @media (min-width:$screen-md) {
        display: flex;
        justify-content: center;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: center;
        gap: 12px;
      }
    }
  }

  .featured_image {
    width: 100%;
    object-fit: cover;
    display: none;

    @media (min-width:$screen-md) {
      display: block;
    }
  }

  .featured_image_mobile {
    width: 100%;
    height: auto;

    @media (min-width:$screen-md) {
      display: none;
    }

  }

  .content {
    img {
      width: 122px;
      height: auto;
    }
  }

}
