.thank-you-section {
  padding: 144px 0 80px 0;
  background: #321B5F;
  position: relative;
  text-align: center;

  @media (min-width: $screen-md) {
    padding: 144px 0;
  }

  h1 {
    @include pop(400, 35, 124%, normal);
    letter-spacing: -0.7px;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, #FFFFFF 0.01%, rgba(255, 255, 255, 0.6) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    margin-bottom: 16px;
    text-align: center;

    @media (min-width: $screen-md) {
      @include pop(400, 53, 124%, normal);
      letter-spacing: -1.06px;
      margin-top: 40px;
    }

  }


  .sub_title {
    @include fig(400, 20, 24.8px, normal);
    color: rgba(255, 255, 255, 0.8);
    text-align: center;

    @media (min-width: $screen-md) {
      @include pop(400, 23, 28.5px, normal);
      color: rgba(255, 255, 255, 0.8);
      max-width: 750px;
      margin: 0 auto;
    }

  }

  .button {
    display: table;
    height:64px;
    padding: 0px 32px 0px 32px;
    border-radius: 6px;
    background:#27B989;
    transition: 0.3s;
    @include pop(400, 16, 64px, normal);
    color: #FFF;
    cursor: pointer;
    text-align: center;
    margin:  0 auto;
    text-decoration: none;
    margin-top: 48px !important;
    position: relative;
    z-index: 2;


    &:hover {
      background: #24a87d;
    }

  }

  .intro_image {
    max-width: 1200px;
    margin: 0 auto;
    transform: scale(2.5);
    padding: 150px 0 130px 0;
    width: 100%;

    @media (min-width: $screen-md) {
      padding: 0;
      transform: unset;
    }

  }
  

  h2 {
    @include pop(400, 35, 124%, normal);
    letter-spacing: -0.7px;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, #FFFFFF 0.01%, rgba(255, 255, 255, 0.6) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-align: center;
  }

  .icon_list {
    display: flex;
    flex-direction: column;
    padding: 40px 0;
    max-width: 1200px;
    margin: 0 auto;

    @media (min-width: $screen-md) {
      flex-direction: row;
    }

    li {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      align-items: center;

      .icon {
        max-width: 80px;
        margin-bottom: 13px;
      }

      h3 {
        @include pop(400, 23, 136%, normal);
        color: #fff;
        letter-spacing: -0.46px;
      }

      &:before {
        content: '';
        width: 2px;
        height: 40px;
        background: #7a50ce;
        margin: 24px 0;

        @media (min-width: $screen-md) {
          content: '';
          width: 70%;
          height: 2px;
          background: #7a50ce;
          position: relative;
          top: 38px;
          left: -50%;
          margin: unset;
        }

      }

      &:first-of-type {
        &:before {
          content: none;
        }
      }

    }

  }

  &:before {
    content: '';
    background-image: url('/wp-content/themes/acro/images/thank-you-bg.png');
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: cover;
    bottom: 0;
    filter: blur(100px);
  }

}