.info_blocks_container {
  background: #EEEFF2;
  padding: 96px 20px;

  .container {
    padding: 0;
  }

  @media (min-width:$screen-md) {
    padding: 96px 40px;
  }

  h2 {
    @include pop(400,29,124%,normal);
    text-align: center;
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    transition: 1.5s;
    margin-bottom: 16px;

    br {
      display: none;
    }

    @media (min-width:$screen-md) {
      @include pop(400,44,124%,normal);

      br {
        display: block;
      }
    }

  }

  h3 {
    @include pop(400,23,136%,normal);
    text-align: center;
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    transition: 1.5s;
    margin-bottom: 20px;

    @media (min-width:$screen-md) {
      @include pop(400,23,136%,normal);
    }

  }

  .info_blocks {
    padding-top: 20px;
    display: none;
    flex-direction: row;
    gap: 1px;
    flex-wrap: wrap;


    @media (min-width:$screen-md) {
      display: flex;
      box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1), 0px 24px 60px rgba(6, 47, 125, 0.05), 0px 12px 24px rgba(27, 59, 119, 0.05);
    }

    .info_block {
      flex-grow: 1;
      width: 33%;
      background: #fff;
      padding: 32px;

      .icon {
        max-width: 56px;
        margin-bottom: 16px;
      }

      h4 {
        @include pop(400,13,124%,normal);
        color:#777184;
        margin-bottom: 16px;
        margin-top: 32px;
        text-align: left;
        text-transform: uppercase;
      }

      h3 {
        @include pop(400,23,136%,normal);
        text-align: center;
        background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        margin-bottom: 16px;
        text-align: left;
      }

      p {
        @include fig(400,18,156%,normal);
        color: #4F4B58;
      }

      a {
        @include pop(400,15,100%,normal);
        margin-top: 23px;
        display: inline-block;
        padding: 16px 40px;
        border: 1.6px solid #5A6272;
        border-radius: 6px;
        text-decoration: none;
        color: #5A6272;

        @media (min-width:$screen-md) {
          padding: 16px 28px;
        }

      }

    }

  }

  .info_blocks_slider {
    display: flex;

    @media (min-width:$screen-md) {
      display: none;
    }

    .swiper-slide {
      //height: auto;

      .info_block {
        width: unset;
        box-shadow: 0 2px 4px rgba(7,4,146,.1), 0 24px 60px rgba(6,47,125,.05), 0 12px 24px rgba(27,59,119,.05);
        min-height: 320px;
      }

    }

  }

}

.bigger_gaps {
  background: #f1f3f7 !important;
  
  .info_blocks {
    gap: 15px;
    box-shadow: none;

    .info_block {
      width: calc(33% - 6px);
      box-shadow: 0 2px 4px rgba(7,4,146,.1), 0 24px 60px rgba(6,47,125,.05), 0 12px 24px rgba(27,59,119,.05);

      h3 {
        @include pop(400,29,124%,normal);
      }

    }

  }

}

.no_slider {

  .info_blocks_slider {
    display: none !important;
  }

  .info_blocks_desk {
    display: flex !important;
    flex-direction: column !important;

    @media (min-width:$screen-md) {
      flex-direction: row !important;
    }

    .info_block {
      width: 100%;

      @media (min-width:$screen-md) {
        width: 33%;
      }

    }

  }

}