.simple_header {
  padding: 144px 0 25px 0;
  background: #eeeff2;

  .top_title {
    @include fig(400, 20, 24.8px, normal);
    color: #5A6272;
    text-align: center;
    margin-bottom: 16px;

    @media (min-width: $screen-md) {
      @include pop(400, 20, 28.5px, normal);
      color: #5A6272;
      margin: 0 auto 16px auto;
    }

  }


  h1 {
    @include pop(400, 35, 124%, normal);
    letter-spacing: -0.7px;
    background:  linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    margin-bottom: 32px;
    text-align: center;

    @media (min-width: $screen-md) {
      @include pop(400, 53, 124%, normal);
      letter-spacing: -1.06px;
    }

  }

  .sub_title {
    @include fig(400, 20, 24.8px, normal);
    color: #5A6272;
    text-align: center;

    @media (min-width: $screen-md) {
      @include pop(400, 20, 28.5px, normal);
      color: #5A6272;
      margin: 0 auto;
    }

  }

  .icon {
    max-width: 416px;
    margin: 32px auto 25px auto;
    display: block;
    width: 100%;
    height: auto;
  }


}