.white_label_banner {
  background: linear-gradient(
    0deg,
    rgba(242, 244, 248, 1) 0%,
    rgba(231, 227, 237, 1) 75%,
    rgba(255, 255, 255, 1) 100%
  );
  min-height: 100vh;
  padding: 105px 0 0;
  position: relative;
  margin-top: 37px;
  @media (min-width: $screen-sm) {
    padding-bottom: 440px;
    min-height: 70vh;
  }

  @media (min-width: $screen-md) {
    padding-bottom: 360px;
    min-height: 70vh;
  }

  @media (min-width: $screen-mlg) {
    padding-bottom: 470px;
    min-height: 70vh;
  }

  .small_title {
    color: #5a6272;
    @include fig(400, 20, 21px);
    margin-bottom: 24px;
    text-align: center;
    @media (min-width: $screen-md) {
      line-height: 31px;
    }

  }

  .big_title {
    @include titleGrad();
    @include pop(400, 35, 43px);
    letter-spacing: -0.02em;
    text-align: center;
    margin-bottom: 48px;
    @media (min-width: $screen-md) {
      font-size: 44px;
      line-height: 49px;
    }
  }

  .btn_group {
    align-items: center;
    justify-content: center;

    a {
      max-width: 196px;
      width: 100%;
      text-align: center;
    }
  }

  .section_image {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 37vh;
    background-position: -460px bottom;
    background-size: auto 100%;
    background-repeat: no-repeat;
    @media (min-width: $screen-md) {
      background-size: 100% auto;
      height: 300px;
      background-position: center bottom;
    }
    @media (min-width: $screen-lg) {
      height: 45vh;
    }
  }
}
