.two_column_image_timeline {
  overflow: hidden;

  .two_column_image_timeline_top {
    min-height: 80vh;

    .featured_image_top {
      display: none;
      @media (min-width: $screen-md) {
        align-items: flex-start;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-end;
      }

      img {
        width: 250%;
        transform: translateY(-44%);
        @media (min-width: $screen-md) {
          width: 150%;
          transform: translateY(12%);
        }

        @media (min-width: $screen-lg) {
          width: 150%;
          transform: translateY(20%);
        }
      }
    }

    .title_area {
      @media (min-width: $screen-md) {
        transform: translateY(100px);
        opacity: 0;
        visibility: hidden;
      }

      h2 {
        text-align: center;
        @include pop(400, 35, 43px);
        letter-spacing: -0.02em;
        @include titleGrad();
        margin-bottom: 11px;
        @media (min-width: $screen-md) {
          font-size: 53px;
          line-height: 65px;
        }

        br {
          @media (min-width: $screen-md) {
            display: none;
          }
        }
      }

      p {
        @include pop(400, 20, 27px);
        letter-spacing: -0.02em;
        text-align: center;
        @include titleGrad();
        margin-bottom: 11px;
        @media (min-width: $screen-md) {
          font-size: 23px;
          line-height: 31px;
        }
      }
    }
  }

  .desktop {
    display: none;
    @media (min-width: $screen-md) {
      display: flex;
    }
  }

  .mobile {
    @media (min-width: $screen-md) {
      display: none;
    }
  }

  .img_bottom {
    @media (max-width: $screen-md) {
      margin-right: -24px;
    }

    .mobile {
      left: -20px;
      position: relative;
      width: calc(100% + 20px);
    }

    img {
      width: 100%;
      height: auto;
    }

  }
}
.two_column_image_timeline_wrapper {
  position: relative;

  .timeline_row {


    @media (min-width: $screen-md) {
      &:last-of-type {
        .container {
          margin-bottom: -50px;
        }
      }
    }

    .container {
      min-height: 110vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;
      padding: 0;
      @media (min-width: $screen-md) {
        height: 100vh;
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: row;
        flex-wrap: wrap;

      }

      &:before {
        content: "";
        position: absolute;
        left: 17px;
        width: 1px;
        background: #babfc9;
        height: 100vh;
        top: 0px;
        @media (min-width: $screen-md) {
          left: 70px;
        }
      }
    }

    &:nth-child(2),
    &:nth-child(4) {
      background: linear-gradient(
        180deg,
        #e9ecf2 0%,
        #e7e3ed 100%,
        #e8eaed 100%
      );
    }

    &:first-child {
      .container {
        &:before {
          display: none;
        }

        .timeline_col_description {
          &:before {
            content: "";
            position: absolute;
            left: 17px;
            width: 1px;
            background: #babfc9;
            height: 400%;
            top: 46px;
            @media (min-width: $screen-md) {
              left: 70px;
            }
          }
        }
      }
    }

    &:last-child {
      .container {
        &:before {
          display: none;
        }
        .timeline_col_description {
          &:before {
            content: "";
            position: absolute;
            left: 17px;
            width: 1px;
            background: #babfc9;
            height: 300%;
            bottom: calc(100% - 46px);
            @media (min-width: $screen-md) {
              left: 70px;
            }
          }
        }
      }
    }
  }

  .timeline_col_description {
    position: relative;
    padding: 23px 41px 0 47px;
    order: 2;
    @media (min-width: $screen-md) {
      order: 1;
      width: 100%;
      max-width: 40%;
      padding: 0 0 0 106px;
    }

    &:after {
      position: absolute;
      top: 46px;
      left: 14px;
      content: "";
      width: 7px;
      height: 7px;
      border-radius: 100px;
      background: #6436bf;
      @media (min-width: $screen-md) {
        left: 67.5px;
        width: 7.5px;
        height: 7.5px;
      }
    }
  }
  .timeline_col_image {
    order: 1;
    @media (min-width: $screen-md) {
      order: 2;
      width: 100%;
      max-width: 60%;
    }




    img {
      width: 130%;
      height: auto;
    }

  }


  .description {
    @include pop(400, 29, 35px);
    @include titleGrad();
    @media (min-width: $screen-md) {
      max-width: 382px;
    }
  }

  .desktop {
    display: none;
    @media (min-width: $screen-md) {
      display: flex;
      position: relative;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  .mobile {
    @media (min-width: $screen-md) {
      display: none;
    }
  }

  .pinnableSection {
    @media (max-width: $screen-md) {
      display: none;
    }
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: flex-start;

    .pinnableSection_img {
      width: 100%;
      height: 100vh;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      &.active {
        img {
          opacity: 1;
          visibility: visible;
        }
      }
    }

    img {
      position: absolute;
      right: 0;
      visibility: hidden;
      opacity: 0;
      transition: ease all 0.3s;
      width: 110%;
      @media (min-width: $screen-lg) {
        width: 125%;
      }
    }
  }
  .partners {
    display: flex;
    align-items: center;
    padding: 94px 20px 131px;
    flex-direction: column;
    order: 3;
    @media (min-width: $screen-md) {
      padding: 0 106px 0px 106px;
      min-height: 2px;
      width: 100%;
      align-items: flex-start;
      margin-bottom: -40px;
    }

    .title_area {
      margin-bottom: 40px;
      @media (min-width: $screen-md) {
        margin-bottom: 31px;
      }
      @media (min-width: $screen-md) {
        br {
          display: none;
        }
      }
    }

    h3 {
      @include pop(100, 23, 32px);
      letter-spacing: -0.02em;
      @include titleGrad();
      text-align: center;
      @media (min-width: $screen-md) {
        font-size: 18px;
        line-height: 22px;
        text-align: left;
      }
    }

    .partner_logos {
      display: flex;
      flex-wrap: wrap;
      margin-left: -25px;
      margin-right: -25px;
      @media (min-width: $screen-md) {
        flex-wrap: nowrap;
        width: 100%;
        justify-content: space-between;
        gap: 50px;
      }
    }

    .logo {
      width: 100%;
      max-width: 50%;
      display: flex;
      align-items: center;
      margin-bottom: 56px;
      padding: 0 25px;
      @media (min-width: $screen-md) {
        max-width: 100%;
        margin-bottom: 0;
        width: auto;
      }

      &:first-child {
        img {
          @media (max-width: $screen-md) {
            max-height: 40px;
          }
        }
      }

      &:last-child {
        img {
          @media (max-width: $screen-md) {
            max-height: 30px;
            margin-left: auto;
          }
        }
      }

      img {
        width: auto;
        // max-height: 28px;
        max-width: 100%;
        @media (min-width: $screen-md) {
          max-width: auto;
        }
      }
    }
  }

  #brand-image-notPin {
    width: 100%;
    max-width: 50%;
  }

  #brand-image-pin {
    width: 100%;
    max-width: 40%;
  }
}
