.single_feature {
  padding: 40px 20px 0 20px;
  background: #fff;
  box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
  margin: 0 0 0 0;
  border-radius: 10px;

  @media (min-width: $screen-md) {
    padding: 40px 40px 0px 40px;
    margin-bottom: 0;
    border-radius: 24px 24px 0 0;
  }

  .stepped_form {
    padding: 0;
    position: relative;
    left: -20px;
    width: calc(100% + 40px);

    @media (min-width: $screen-md) {
      position: unset;
      left: unset;
      width: unset;
    }

  }

  .two_column_feature_component .image_column img {
    max-width: 100%;
  }

  .feature_grid_element:last-of-type {
    padding-bottom: 0;
  }

  .feature_grid_element .grid_elements .grid_element {
    .top_part {
      .icon {
        margin-bottom: 8px;

        @media (min-width: $screen-md) {
          position: unset;
          left: unset;
          width: unset;
          margin-bottom: 16px;
        }
      }

      .number {
        margin-bottom: 5px;

        @media (min-width: $screen-md) {
          margin-bottom: 21px;
        }
      }
    }

    .content_part {
      .title {
        margin-bottom: 8px;
      }

    }
  }
}

.feature-icon {
  display: none;

  @media (min-width: $screen-md) {
    width: 80px;
    height: 80px;
    margin: 16px auto;
    display: block;
  }
}

.single-features {
  /*background: #EEEFF2;*/
}

.feature_header_container {
  //max-height: 100%;
  overflow-y: auto;
  display: flex;
  justify-content: center;
  position: sticky;
  width: var(--viewport-width);
  margin-right: -50%;
  right: -50%;
  margin-left: -50%;
  bottom: 0;
  left: -50%;
  background: #eeeff2;
  box-shadow: 0px -2.77px 2.21px 0px #7A7C7E08, 0px -6.65px 5.32px 0px #7A7C7E08, 0px -12.52px 10.02px 0px #7A7C7E08, 0px -22.34px 17.87px 0px #7A7C7E08, 0px -41.78px 33.42px 0px #7A7C7E08, 0px -100px 80px 0px #7A7C7E08;
  z-index: 9;
  padding-top: 0px;
  margin-top: 0px;
  border-radius: 24px 24px 0 0;
  transition: all 0.3s ease;

  &--bottom {
    padding-top: 0;
    margin-top: 0px;


  }

  @media (max-width: 1024px) {
    width: 100%;
    left: 0;
    right: 0;
    margin-left: 0;
    margin-right: 0;
    transform: translateY(0%);
    padding-top: 0px;
  }


  @media (max-width: 768px) {
    width: 100%;
    left: 0;
    right: 0;
    margin-left: 0;
    margin-right: 0;
    transform: translateY(0%);
    padding-top: 0px;
    margin-top: -30px;
  }

  @media (min-width: $screen-md) {
    position: sticky;
    bottom: 0;
    left: 0;
  }
}

.feature_header {
  overflow: hidden;
  border-radius: 10px;
  width: 100%;
  padding: 10px 14px;

  @media (min-width: $screen-md) {
    display: inline-flex;
    gap: 10px;
    width: unset;
  }

  &__mobile_menu {
    background: #532D9F;
    color: #fff;
    padding: 19.18px;
    display: flex;
    justify-content: center;
    border-radius: 7px;
    font-family: Figtree;
    font-size: 20px;
    font-weight: 500;
    line-height: 25.2px;
    text-align: left;


    @media (min-width: $screen-md) {
      display: none;
    }

    &--hidden {
      .feature_header__mobile_menu-close {
        display: none !important;
      }

      .feature_header__mobile_menu-title {
        display: none !important;
      }

      a {
        display: none !important;

        @media (min-width: $screen-md) {
          display: flex !important;
        }
      }
    }
  }

  .hidden {
    display: none !important;
  }

  .feature_header_title {
    padding: 8px 8px 14px 8px;
    @include pop(400, 19, 124%, normal);
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .feature_header__mobile_menu-close {
    height: 34px;
    width: 34px;
    margin-left: auto;
    margin-bottom: 14px;
    margin-top: 12px;
    display: flex;
    align-items: center;
    justify-content: center;

    svg {
      width: 17px;
      height: 17px;
    }

    @media (min-width: $screen-md) {
      display: none;
    }

  }

  .feature_header__mobile_menu-title {
    display: block;
    font-family: Figtree;
    font-size: 20px;
    font-weight: 500;
    line-height: 25.2px;
    text-align: center;
    color: #301565;
    margin-bottom: 14px;

    @media (min-width: $screen-md) {
      display: none;
    }
  }

  a {
    display: flex;
    padding: 14px 8px;
    flex-direction: row;
    align-items: center;
    gap: 8px;
    flex: 1 0 0;
    align-self: stretch;
    text-align: left;
    text-decoration: none;
    @include fig(500, 18, 126%, normal);

    border-radius: 10px;
    margin-bottom: 9px;
    background: white;
    justify-content: center;



    @media (min-width: $screen-md) {
      margin-bottom: 0;
      padding: 22px;
      max-height: 65px;
      align-self: center;
      gap: 0;
    }

    .icon {
      width: 21px;
      height: 21px;
      max-width: 21px;

      svg {
        width: 21px;
        height: 21px;
      }
    }

    .title {
      @media (min-width: $screen-md) {
        max-width: 0;
        display: inline-block;
        vertical-align: bottom;
        overflow: hidden;
        white-space: nowrap;
        transform: translateX(0);
        will-change: transform, opacity, max-width;
        transition: opacity 0.3s, max-width 0.3s, transform 0s 0.3s;
        font-size: 17px;
        opacity: 0;
      }
    }

    &:hover {
      //gap: 8px;
      max-height: unset;
      align-self: unset;

      .title {
        font-size: 20px;

        @media (min-width: $screen-md) {
          transform: translateX(8px);
          opacity: 1;
          white-space: nowrap;
          max-width: 500px;
          background: linear-gradient(180deg, #2d3442 0, #6c5689 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          will-change: transform, opacity, max-width;
          transition: transform 0s, opacity 0.5s, max-width 0.7s;
          line-height: normal;
          color: #5A6272;
          font-size: 17px;
        }
      }

    }

    &:last-of-type {
      margin-bottom: 0;
    }

  }

  .active {
    &:hover {
      .title {
        transform: translateX(0);
        background: unset;
        -webkit-background-clip: unset;
        -webkit-text-fill-color: unset;
        color: white;
        font-size: 18px;
      }
    }

    background: #532D9F;
    color: #fff;
    gap: 8px;
    max-height: unset;

    svg {
      path {
        stroke: #fff !important;
      }

      rect {
        stroke: #fff !important;
      }

      ellipse {
        stroke: #fff !important;
      }
    }

    @media (min-width: $screen-md) {
      .icon {
        width: 21px;
        height: 21px;
        max-width: 21px;

        svg {
          width: 21px;
          height: 21px;
        }
      }
    }


    .title {
      display: block;
      white-space: nowrap;
      max-width: 500px;
      opacity: 1;
      overflow: unset;
      font-size: 18px;
    }

  }

}

.footer_title {

  @include pop(400, 29, 124%, normal);
  background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 24px;
  text-align: center;
  font-size: 18px;
  padding-top: 60px;

  @media (min-width: $screen-md) {
    font-size: 29px;
  }
}


.feature_footer {
  padding-bottom: 60px;
  display: flex;
  gap: 4px;

  @media (min-width: $screen-md) {
    gap: 8px;
  }

  @media(max-width: 767px) {
    flex-direction: column;
    padding-bottom: 30px;
  }

  a {
    display: flex;
    padding: 10px 8px 10px 8px;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    flex: 1 0 0;
    align-self: stretch;
    text-align: center;
    text-decoration: none;
    @include fig(500, 13, 126%, normal);
    background: #fff;
    box-shadow: 0 12px 24px 0 rgba(27, 59, 119, .05), 0 24px 60px 0 rgba(6, 47, 125, .05), 0 2px 4px 0 rgba(7, 4, 146, .1);

    @media(max-width: 767px) {
      color: #5A6272;
      text-align: center;
      font-family: Figtree;
      font-size: 16px;
      font-style: normal;
      font-weight: 500;
      line-height: 126%;
      /* 20.16px */
      padding: 22px 8px;
      border-radius: 10px;
      min-height: 104px;
    }

    &:first-of-type {
      //border-top-left-radius: 5px;
      //border-bottom-left-radius: 5px;
    }

    &:last-of-type {
      //border-top-right-radius: 5px;
      //border-bottom-right-radius: 5px;
    }

    @media (min-width: $screen-md) {
      padding: 32px 8px 24px 8px;
      border-radius: 14px;
      gap: 12px;
    }

    &:last-of-type {
      border-right: none;
    }

    .title {
      color: #5A6272;

      @media (min-width: $screen-md) {
        font-size: 17px;
      }
    }

    .icon {
      width: 48px;

      @media (min-width: $screen-md) {
        width: 48px;
        height: 48px;
      }

      @media(max-width: 767px) {
        width: 36px;
        height: 36px;
      }

      img {
        width: 100%;

        @media (min-width: $screen-md) {
          width: 100%;
        }
      }
    }
  }
}

.features-container {
  display: block;
  padding: 0 10px;

  @media (max-width: 767px) {
    padding: 0px;
  }

  @media (min-width: $screen-md) {
    padding: 0px 40px 15px 40px;
    gap: 40px;
  }

  .page-title {
    display: none;

    @media (min-width: $screen-md) {
      display: block;
      font-family: Poppins, sans-serif;
      text-align: center;
      font-size: 53px;
      font-weight: 400;
      background: linear-gradient(180deg, #2d3442 0%, #6c5689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      margin-bottom: 44px;
      margin-top: 44px
    }

  }
}

.feature_power_stats {
  margin: 80px 0;

  .power-stats__title {
    font-size: 35px;
    line-height: 43.4px;
  }

  .power-stats {
    padding: 0 10px;
  }

  @media (min-width: $screen-md) {

    .power-stats {
      padding: 70px 0px 40px;

      &__stats {
        max-width: unset;
        width: unset;
        margin: 0 40px 44px;
      }
    }

    .power-stats__title {
      font-size: 44px;
      line-height: 49.28px;
    }
  }

}


.features_footer {
  width: 100%;
  display: flex;
  justify-content: center;
  background: #EEEFF2;

  .features_footer_cont {
    width: calc(100% - 320px);

    @media(min-width: 1441px) {
      width: calc(100% - 530px);
    }

    @media(max-width: 1440px) {
      width: calc(100% - 320px);
    }

    @media(max-width: 1024px) {
      width: calc(100% - 35px);
    }

    @media(max-width: 767px) {
      width: calc(100% - 35px);
    }
  }
}



.footer_title {
  @media(max-width: 767px) {
    text-align: center;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: 124%;
    /* 34.72px */
    letter-spacing: -0.56px;
  }
}

@media (min-width: 1800px) {
  .only_single_features .faq.faqv2 .faq_container {
    width: calc(100% - 530px);
  }
}

@-moz-document url-prefix() {
  .feature_header_container {
    overflow: initial;
    width: 100%;
    margin: 0 auto;
    left: 0;
    right: 0;
    margin-left: 0;
    margin-right: 0;
  }

  .single-features {
    overflow-x: hidden;
  }

  .feature_header {
    z-index: 2;
    overflow: initial;
  }
}

@media (min-width: 1025px) and (max-width: 1279px) {
  @-moz-document url-prefix() {
    .feature_header_container {
      &::before {
        content: "";
        position: absolute;
        top: 0;
        width: 5.5%;
        left: -5.5%;
        background: #eeeff2;
        height: 100%;
      }

      &::after {
        top: 0;
        content: "";
        position: absolute;
        width: 5.5%;
        right: -5.5%;
        background: #eeeff2;
        height: 100%;
      }
    }

    .single-features {
      overflow-x: hidden;
    }

  }
}

