.app_blocks_container {
  padding: 107px 20px 80px 20px;

  @media (min-width: $screen-md) {
    flex-direction: row;
    align-items: center;
    padding: 118px 40px;
    overflow: hidden;
    position: relative;
    z-index: 999;
  }

  h1 {
    @include pop(400, 29, 124%, normal);
    letter-spacing: -0.02em;
    background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-align: center;
    margin-bottom: 10px;

    @media (min-width: $screen-md) {
      @include pop(400, 44, 112%, normal);
    }

    br {
      @media (min-width: $screen-md) {
        display: none;
      }
    }

    &.as_header {
      @media (max-width: $screen-md) {
        font-size: 44px;
      }
    }
  }

  h2 {
    @include pop(400, 29, 124%, normal);
    letter-spacing: -0.02em;
    background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-align: center;
    margin-bottom: 10px;

    @media (min-width: $screen-md) {
      @include pop(400, 44, 112%, normal);
    }

    br {
      @media (min-width: $screen-md) {
        display: none;
      }
    }

    &.as_header {
      @media (max-width: $screen-md) {
        font-size: 44px;
      }
    }
  }

  h3 {
    @include fig(400, 20, 156%, normal);
    color: #5a6272;
    text-align: center;
    margin-bottom: 20px;

    @media (min-width: $screen-md) {
      margin-bottom: 30px;
    }

  }


  .app_blocks {
    display: flex;
    flex-direction: column;
    gap: 15px;

    @media (min-width: $screen-sm) {
      flex-direction: row;
    }

    .app_block {
      background: #dde0e7;
      padding: 32px;
      display: flex;
      flex-direction: column;
      overflow: hidden;
      width: 100%;
      @media (min-width: $screen-sm) {
        flex-direction: row;
      }
      @media (min-width: $screen-mlg) {
        padding: 48px 60px;
      }

      .content_column {
        @media (min-width: $screen-sm) {
          flex-basis: 50%;
          display: flex;
          flex-direction: column;
        }

        .icon {
          max-width: 48px;
          margin-bottom: 24px;
        }

        h4 {
          @include pop(400, 29, 124%, normal);
          letter-spacing: -0.02em;
          background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          background-clip: text;
          text-fill-color: transparent;
          margin-bottom: 20px;
          text-align: left;
        }

        p.description {
          @include fig(500, 20, 24px);
          color: #4f4b58;
          margin-bottom: 30px;
          @media (min-width: $screen-md) {
            margin-bottom: 74px;
          }

          br {
            @media (min-width: $screen-md) {
              display: none;
            }
          }
        }

        ul {
          margin: 0;
          padding: 0;
          margin-bottom: 26px;

          li {
            @include fig(400, 18, 20px, normal);
            color: #8c8698;
            margin-bottom: 16px;
            display: flex;
            flex-direction: row;
            gap: 16px;
            align-items: center;
            opacity: 0.4;

            svg {
              max-width: 24px;
            }
          }

          .active {
            color: #1e8f69;
            opacity: 1;
          }
        }

        .link {
          @include pop(400, 17, 124%, normal);
          letter-spacing: 0.02em;
          text-transform: uppercase;
          color: #6436bf;
          text-decoration: none;
          margin-bottom: 5px;
          display: block;
          @media (min-width: $screen-md) {
            margin-bottom: 0;
            margin-top: auto;
          }
        }

        .app_block_button_group {
          display: flex;
          justify-content: space-between;

          a {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            max-width: calc(50% - 3px);
            border-radius: 4px;

            img {
              max-height: 49px;
              width: 100%;
            }
          }
        }
      }

      .image_column {
        position: relative;
        min-height: 250px;
        @media (min-width: $screen-sm) {
          flex-basis: 50%;
          padding-left: 40px;
        }

        img {
          position: absolute;
          width: 100%;
          margin-top: 30px;
          height: auto;
          //box-shadow: 7.049548149108887px 4.086097240447998px 6.518518447875977px 0px rgba(29, 22, 43, 0.04), 33.32513427734375px 19.316097259521484px 25.481481552124023px 0px rgba(29, 22, 43, 0.06), 86.51718139648438px 50.147560119628906px 80px 0px rgba(29, 22, 43, 0.10);
          //border-radius: 15px;

          @media (min-width: $screen-md) {
            width: 85%;
            padding-left: 0;
            margin: 0 auto;
            left: 0;
            right: 0;
            //box-shadow: 7.049548149108887px 4.086097240447998px 6.518518447875977px 0px rgba(29, 22, 43, 0.04), 33.32513427734375px 19.316097259521484px 25.481481552124023px 0px rgba(29, 22, 43, 0.06), 86.51718139648438px 50.147560119628906px 80px 0px rgba(29, 22, 43, 0.10);
            //border-radius: 15px;
          }
        }
      }
    }
  }
}
