.feature_grid_element {
  padding: 40px 0 0 0;

  @media (min-width: $screen-md) {
    padding: 40px 0;
  }

  h2 {
    @include pop(400, 29, 124%, normal);
    letter-spacing: -0.7px;
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 12px;
    text-align: center;

    @media (min-width: $screen-md) {
      @include pop(400, 35, 124%, normal);
      letter-spacing: -1.06px;
      text-align: center;
    }

  }

  .sub_title {
    @include fig(400, 20, 156%, normal);
    color: #4F4B58;
    margin-bottom: 48px;
    text-align: center;
  }

  .grid_elements {
    display: flex;
    flex-direction: column;
    position: relative;

    @media (min-width: $screen-md) {
      flex-direction: row;
    }

    .grid_element {
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      flex: 1 0 0;
      padding: 0 0px 48px 12px;

      position: relative;

      @media (min-width: $screen-md) {
        padding: 0 0px 48px 20px;
      }

      .top_part {
        margin-left: 8px;
        .number {
          @include pop(400, 35, 124%, normal);
          letter-spacing: -0.7px;
          color: #6436BF;
          margin-bottom: 35px;
        }

        .icon {
          margin-bottom: 35px;
          display: block;
        }

      }

      .content_part {
        margin-left: 8px;
        margin-top: 8px;
        @media (min-width: $screen-md) {
          margin-right: 32px;
        }

        &--number {
          margin-right: 32px;
          margin-top: 0;
        }
        .title {
          @include pop(400, 23, 136%, normal);
          letter-spacing: -0.46px;
          background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
          background-clip: text;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          margin-bottom: 35px;
          text-align: left;
        }

        .content {
          @include fig(400, 17, 156%, normal);
          color: #4F4B58;
        }

      }

      &:last-of-type {

        &:before {
          bottom: 0;
        }

      }

      &:before {
        content: '';
        width: 1.2px;
        left: 0px;
        height: 100%;
        background: #BABFC9;
        position: absolute;
        bottom: -10%;

        @media (min-width: $screen-md) {
          height: 90%;
          bottom: 0;
        }

      }

      &:after {
        content: '';
        width: 7.5px;
        height: 7.5px;
        border-radius: 100%;
        background: #6436BF;
        position: absolute;
        left: -3px;
        top: 8%;
      }

    }

    &:after {
      content: '';
      width: calc(100% + 80px);
      left: -40px;
      height: 1.2px;
      background: #BABFC9;
      position: absolute;
      bottom: 0;
    }

  }

}

.full_border_top {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    width: calc(100% + 80px);
    left: -40px;
    top: 0;
    height: 1.2px;
    background: #D1D5DB;
  }

}