.linkup_device_container {
  background: #eeeff2;
  padding-top: 143px;

  @media (min-width: $screen-md) {
    padding-top: unset;
  }

  &.width_thin {
    background: #f1f3f7 !important;
    @media (min-width: $screen-md) {
      padding: 92px 0;
    }
    .container {
      max-width: 1140px;
    }

    h2 {
      @media (min-width: $screen-md) {
        font-size: 29px !important;
        line-height: 35px !important;
      }
    }
  }

  @media (min-width: $screen-md) {
    padding: 226px 80px;
  }

  .linkup_device_inner_container {
    display: flex;
    flex-direction: column;
    box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1),
    0px 24px 60px rgba(6, 47, 125, 0.05),
    0px 12px 24px rgba(27, 59, 119, 0.05);
    border-radius: 0px;
    gap: 45px;
    background: #fff;

    @media (min-width: $screen-md) {
      flex-direction: row;
      gap: 54px;
    }

    @media (min-width: $screen-mlg) {
      height: 321px;
    }

    .image-column {
      flex-basis: 50%;
      display: flex;
      position: relative;
      min-height: 185px;

      @media (min-width: $screen-sm) {
        min-height: 375px;
      }

      @media (min-width: $screen-mlg) {
        min-height: 321px;
        justify-content: center;
      }

      img {
        width: 100%;
        height: auto;
        position: absolute;
        top: -42px;
        right: 0;

        @media (min-width: $screen-sm) {
          transform: scale(1.0);
          position: relative;
        }

        @media (min-width: $screen-md) {
          transform: translate(-50%, -50%);
          width: auto;
          height: 100%;
          top: 50%;
          left: 50%;
          position: absolute;
        }
        @media (min-width: $screen-mlg) {
          transform: scale(1.2);
          width: auto;
          height: 100%;
          top: unset;
          left: unset;
          position: relative;
        }
      }
    }

    .content-column {
      flex-basis: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 32px;
      align-items: flex-start;

      .icon {
        max-height: 32px;
        margin-bottom: 24px;
        width: auto;
      }

      h2 {
        @include pop(400, 35, 124%, normal);
        background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        margin-bottom: 24px;
      }

      .link {
        @include pop(400, 17, 124%, normal);
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: #6436bf;
        text-decoration: none;
      }
    }
  }
}


