.plain_content_component {
  padding: 125px 0 90px 0;

  h1 {
    @include pop(400, 35, 124%, normal);
    letter-spacing: -0.7px;
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 0.75em;
    margin-top: 1.5em;


    @media (min-width: $screen-md) {
      @include pop(400, 40, 124%, normal);
      letter-spacing: -1.06px;
    }

  }

  h2 {
    @include pop(400, 29, 124%, normal);
    letter-spacing: -0.7px;
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 0.75em;
    margin-top: 1.5em;


    @media (min-width: $screen-md) {
      @include pop(400, 35, 124%, normal);
      letter-spacing: -1.06px;
    }

  }

  h3 {
    @include pop(400, 20, 124%, normal);
    letter-spacing: -0.7px;
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 0.75em;
    margin-top: 1.5em;


    @media (min-width: $screen-md) {
      @include pop(400, 25, 124%, normal);
      letter-spacing: -1.06px;
    }

  }


  p {
    @include fig(400, 18, 125%, normal);
    color: #4f4b58;
    margin-bottom: 1em;
  }

  ul {
    padding-left: 25px;

    li {
      @include fig(400, 18, 125%, normal);
      color: #4f4b58;
      list-style: disc;
      margin-bottom: 10px;
    }

  }

  ol {
    padding-left: 25px;

    li {
      @include fig(400, 18, 125%, normal);
      color: #4f4b58;
      list-style: decimal;
      margin-bottom: 10px;
    }

  }

}