.pricing_table {
  background: #eeeff2;
  padding: 160px 0 40px 0;

  .title_section {

    h1 {
      @include pop(400, 35, 124%, normal);
      letter-spacing: -0.7px;
      background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      margin-bottom: 16px;
      text-align: center;

      @media (min-width: $screen-md) {
        @include pop(400, 44, 124%, normal);
        letter-spacing: -1.06px;
      }

    }

    .sub_title {
      @include pop(400, 18, 124%, normal);
      background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      margin-bottom: 40px;
      text-align: center;
    }

  }

  .pricing_table_holder {
    max-width: 1360px;
    margin: 0 auto;

    @media (min-width: $screen-md) {
      box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
      background: #fff;
    }

    .mobile {

      @media (min-width: $screen-md) {
        display: none;
      }

      .pricing_block {
        background: #fff;
        box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);


        .card_title {
          display: flex;
          height: 72px;
          align-items: center;
          justify-content: center;
          @include pop(400, 23, 136%, normal);
          letter-spacing: -0.46px;
          background:linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
          background-clip: text;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }

        .card_features {
          padding: 16px 24px;
          background:#F7FBFB;

          ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
            align-self: stretch;
            padding: 0;
            margin: 0;

            li {
            }

            .active {
              @include fig(500, 15, 28px, normal);
              color: #1E8F69;
            }

            .inactive {
              @include fig(400, 15, 28px, normal);
              color: #A4AAB7;
            }

          }

        }

        .from {
          padding: 8px 24px;
          @include fig(400, 10, 150%, normal);
          text-align: center;
          color: #768093;
          text-transform: uppercase;
        }

        .card_price {
          padding: 14px 24px;
          text-align: center;
          background:#F7FBFB;
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 8px;
          @include pop(400, 35, 43.4px, normal);
          color: #6436BF;

          span {
            @include fig(400, 10, 10px, normal);
            color: #768093;
          }

        }

        .card_cta {
          padding: 16px 24px 32px 24px;

          .button {
            display: block;
            height:56px;
            border-radius: 6px;
            background:#27B989;
            transition: 0.3s;
            @include pop(400, 16, 58px, normal);
            color: #FFF;
            cursor: pointer;
            width: 100%;
            text-align: center;
            text-decoration: none;

            &:hover {
              background: #24a87d;
            }

          }
        }

      }

    }

    .desktop {
      display: none;
      flex-direction: row;

      @media (min-width: $screen-md) {
        display: flex;
      }

      .index_col {
        display: flex;
        flex-direction: column;
        flex-grow: 1;

        .cell {
          display: flex;
          align-items: center;
          justify-content: flex-end;
          padding-right: 24px;
          @include fig(400, 15, 110%, normal);
          color: #1E1C21;

          &:nth-child(even){
            background: #F9FBFB;
          }

        }

      }

      .featured {
        box-shadow: 0px 0.48124998807907104px 2.6015625px 0px rgba(254, 93, 93, 0.06), 0px 2.200000047683716px 7.3125px 0px rgba(254, 93, 93, 0.10), 0px 5.568749904632568px 19.6171875px 0px rgba(254, 93, 93, 0.14), 0px 11px 45px 0px rgba(254, 93, 93, 0.20) !important;
        z-index: 5 !important;


        .title_cell {

            &:first-of-type {
            background: linear-gradient(360deg, #F0328E 0%, #FFC555 100%);
            color: #fff;
            background-clip: unset !important;
            -webkit-text-fill-color : unset;
          }

          svg {
            width: auto;
            margin-right: 25px;
          }

        }

      }

      .prod_col {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        box-shadow: -4px 0px 12px 0 rgba(0,0,0,.09);
        position: relative;
        z-index: 2;

        .cell {
          display: flex;
          align-items: center;
          justify-content: center;

          .from {
            @include fig(400, 10, 150%, normal);
            color: #768093;
            text-transform: uppercase;
          }

          &:nth-child(even){
            background: #F9FBFB;
          }



        }

      }

      .price_cell {
        height: 72px !important;
        flex-direction: row;
        gap: 8px;
        @include pop(400, 35, 43.4px, normal);
        color: #6436BF;

        span {
          @include fig(400, 10, 10px, normal);
          color: #768093;
        }

      }

      .button_cell {
        height: 88px !important;
        align-items: unset !important;
        padding: 16px 24px 0 24px;

        .button {
          height:40px;
          padding: 0px 16px 0px 16px;
          border-radius: 6px;
          background:#27B989;
          transition: 0.3s;
          @include pop(400, 12, 40px, normal);
          color: #FFF;
          cursor: pointer;
          width: 100%;
          text-align: center;
          text-decoration: none;

          &:hover {
            background: #24a87d;
          }

        }

      }

      .title_cell {
        height: 72px !important;
        @include pop(400, 15, 124%, normal);
        background:linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }

      .cell {
        height: 40px;

        .dot {
          border-radius: 100%;

          &.active {
            width: 8px;
            height: 8px;
            background: #27B989;
          }

          &.inactive {
            width: 6px;
            height: 6px;
            background: #C7C9CB;
          }

        }

      }

    }

  }

}