.single-post-cta {
	position: relative;
	box-shadow: 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 12px 24px 0px rgba(27, 59, 119, 0.05);
	margin-bottom: 30px;
	&__bg {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		
		@media (min-width: $screen-sm){
			&.hidden-sm {
				display: none;
			}
		}
	}
	.single-post-cta__inner {
		position: relative;
		z-index: 2;
		padding: 40px 20px;
		text-align: center;
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		flex-direction: column;
		@media (min-width: $screen-sm){
			flex-direction: row;
			&.vertical-align-middle {
				align-items: center;
			}
			&.vertical-align-bottom {
				align-items: flex-end;
			}
		}
		.single-post-cta__title-box {
			width: 100%;
		}
		.single-post-cta__content {
			width: 100%;
		}
		@media (min-width: $screen-sm){
			.single-post-cta__content {
				width: 50%;
				padding-right: 20px;
			}
		}
		@media (min-width: $screen-sm){
			padding: 60px;
			text-align: left;
		}
		@media (min-width: $screen-md){
			padding: 80px 90px;
		}
		&__title {
			@include titleGrad();
			@include pop(400, 23, 1.36);
			margin-bottom: 8px;
			@media (min-width: $screen-sm){
				@include pop(400, 32, 1.24);
				margin-bottom: 10px;
			}
		}
		&__description {
			@include fig(400, 15, 1.36);
			margin-bottom: 20px;
			color: #4F4B58;
			@media (min-width: $screen-sm){
				@include fig(400, 20, 1.56);
				@include titleGrad();
				margin-bottom: 50px;
			}
		}
		&__content {
			margin-bottom: 20px;
			width: 100%;
			@media (min-width: $screen-sm) {
				margin-bottom: 30px;
				padding-right: 20px;
			}
			&, *, p {
				@include fig(400, 15, 1.36);
				color: #4F4B58;
				@media (min-width: $screen-sm){
					@include fig(400, 18, 1.56);
				}
			}
			ul {
				text-align: left;
				list-style-type: none;
				padding: 0 !important;
				margin: 0 !important;
				li {
					position: relative;
					padding: 4px 0 4px 40px;
					list-style: none !important;
					color: #1E8F69 !important;
					margin-bottom: 12px !important;
					&:last-child {
						margin-bottom: 0 !important;
					}
					&::marker {
						opacity: 0;
					}
					&:before {
						content: '';
						display: block;
						width: 24px;
						height: 24px;
						background: url(../../images/svg/checked.svg) no-repeat center;
						top: 4px;
						left: 0;
						position: absolute;
					}
				}
			}
		}
		&__button {
			border-radius: 6px;
			border: 1.6px solid $purple !important;
			padding: 0px 16px;
			@include pop(500, 15, 54px);
			color: #fff;
			background: $purple;
			transition: .3s;
			text-decoration: none !important;
			display: inline-block;
			letter-spacing: 0.15px;
			min-width: 150px;
			text-align: center;
			&:hover {
				background: $purple;
				color: #fff !important;
				border-color: $purple !important;
			}
			@media (min-width: $screen-sm){
				@include pop(500, 18, 66px);
				background: $purple;
				color: #fff !important;
				border-color: $purple !important;
				letter-spacing: 0.2px;
				&:hover {
					background: $green;
					border-color: $green !important;
				}
			}
		}
		&__image {
			//display: inline-block !important;
			width: auto !important;
			max-width: 100%;
			margin: 0 auto 30px auto;
			@media (min-width: $screen-sm){
				max-width: 50%;
				height: auto;
				order: 3;
				margin: 0;
			}
		}
		&.horizontal-align-center {
			@media (min-width: $screen-sm){
				.single-post-cta__inner__image {
					margin: 0 auto;
				}
			}
		}
		&.horizontal-align-right {
			@media (min-width: $screen-sm){
				.single-post-cta__inner__image {
					margin: 0 0 0 auto;
				}
			}
		}
	}
}