.expanded_features_container {
  background: #eeeff2;
  padding: 20px;

  @media (min-width: $screen-md) {
    padding: 96px 40px;
  }

  .container {
    padding: 0;
  }

  h2 {
    @include pop(400, 29, 135%, normal);
    letter-spacing: -0.02em;
    text-align: center;
    background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    margin-bottom: 16px;

    @media (min-width: $screen-md) {
      @include pop(400, 44, 124%, normal);
    }
  }

  h3 {
    @include fig(400, 18, 156%, normal);
    text-align: center;
    color: #4f4b58;
    margin-bottom: 40px;
  }

  .expanded_features {
    display: none;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 16px;

    @media (min-width: $screen-md) {
      display: flex;
    }

    .Bottom {
      display: flex;
      flex-direction: column;

      @media (min-width: $screen-md) {
        min-height: 520px !important;
      }
    }

    .expanded_feature.Left {
      display: flex;
      flex-direction: row;
      // padding: 42px 40px 42px 40px !important;
      overflow: hidden;

      .content {
        width: 50%;
        padding-bottom: 42px !important;
      }

      .image {
        width: 50%;
        height: 100%;
        background-position: right 0 !important;
        background-size: auto 100% !important;

        img {
          height: 100%;
        }
      }
    }

    .swiper-slide {
      height: auto;
    }

    .expanded_feature {
      flex-grow: 1;
      max-width: calc(50% - 8px);
      box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1),
        0px 24px 60px rgba(6, 47, 125, 0.05),
        0px 12px 24px rgba(27, 59, 119, 0.05);
      background: #fff;
      position: relative;

      height: 100%;

      @media (min-width: $screen-md) {
        height: unset;
        // padding: 42px 40px 300px 40px;
      }

      .content {
        padding: 42px 32px 0px 32px;
      }

      .icon {
        max-height: 32px;
        width: auto;
        margin-bottom: 12px;

        @media (min-width: $screen-md) {
          min-height: 62px;
          max-height: 62px;
          width: auto;
        }
      }

      h4 {
        @include pop(400, 29, 124%, normal);
        letter-spacing: -0.02em;
        background: linear-gradient(180deg, #2d3442 0%, #6c7689 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        margin-bottom: 12px;
      }

      .image {
        margin-top: auto;
        background-position: center bottom;
        background-size: cover;
        background-repeat: no-repeat;
        @media (min-width: $screen-md) {
          background-position: left bottom !important;
          // margin-top: 36px;
          background-size: 100% auto !important;
        }

        img.desktop {
          max-height: 400px;
        }
      }

      p {
        @include fig(400, 18, 156%, normal);
        color: #4f4b58;
      }
    }
  }

  .expanded_features_carousel {
    display: block;
    @media (min-width: $screen-md) {
      display: none;
    }

    .expanded_feature {
      max-width: unset;
    }
  }
}
