.stepped_form {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 0;

  @media (min-width:$screen-md) {
    padding: 40px;
  }

  .container {
    padding:0;
  }

  .form_component {
    background: linear-gradient(180deg, #F2F4F8 0%, #E7E3ED 100%, #F4F4F6 100%);
    min-height: 610px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 60px;

    .form_tabs {
      padding: 0 10px;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;

      @media (min-width:$screen-md) {
        padding: 0 40px;
      }

      ul {
        display: flex;
        flex-direction: row;
        gap: 4.25px;

        li {
          height: 60px;
          position: relative;
          flex-grow: 1;
          flex-basis: 25%;


          &:before {
            display: block;
            content: '';
            width: 100%;
            height: 6px;
            top: 0;
            left: 0;
            border-radius: 0px 0px 8px 8px;
          }

          &:after {
            content: '';
            width: 16px;
            height: 16px;
            position: absolute;
            left: 0;
            top: 15px;
          }

          span {
            @include fig(400,13,250%,normal);
            line-height: 259%;
            letter-spacing: -0.51px;
            color: #000;
            padding-left: 20px;

            @media (min-width:$screen-md) {
              letter-spacing: 0.01em;
              color: #000000;
              padding-left: 24px;
            }

          }

        }

        .inactive {

          &:before {
            background: #BABFC9;
          }

          &:after {
            content: url('/wp-content/themes/acro/images/svg/inactive.svg');
          }

        }

        .busy {
          &:before {
            background: #27B989;
          }

          &:after {
            content: url('/wp-content/themes/acro/images/svg/busy.svg');
          }
        }

        .done {

          &:before {
            background: #5A6272;
          }

          &:after {
            content: url('/wp-content/themes/acro/images/svg/done.svg');
          }
        }


      }


    }

    .form_content_blocks {
      display: flex;
      height: 100%;
      width: 100%;
      align-items: center;
      justify-content: center;
      position: relative;

      .md-input-container {
        border-radius: 6px;
        border: 1.2px solid var(--background-400, #A4AAB7);
        padding-top: 0 !important;

        i {
          display: none !important;
        }

        label {
          left: 16px !important;
          top: 15px !important;
        }

        input {
          height: 48px;
        }

      }

      .md-has-value {
        label {
          display: none !important;
        }
      }

      h2 {
        @include pop(400,44,112%,normal);
        text-align: center;
        letter-spacing: -0.02em;
        background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        margin-bottom: 16px;

        @media (min-width:$screen-md) {
          @include pop(400,65,100%,normal);
        }

      }

      h3 {
        @include fig(400,20,156%,normal);
        text-align: center;
        color: #5A6272;
        margin-bottom: 40px;

        @media (min-width:$screen-md) {
          @include fig(400,23,100%,normal);
        }

      }

      .email_block {
        display: flex;
        flex-direction: column;
        padding: 0 20px;
        width: 100%;

        @media (min-width:$screen-md) {
          padding: 0;
        }

        .email_form {
          display: inline-block;
          background: #FFFFFF;
          box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1), 0px 24px 60px rgba(6, 47, 125, 0.05), 0px 12px 24px rgba(27, 59, 119, 0.05);
          border-radius: 10px;
          margin: 0 auto;
          padding: 4px;

          @media (min-width:$screen-md) {
            height: 64px;
            overflow: hidden;
          }

          input {
            @include fig(400,18,100%,normal);
            font-family: 'Figtree';
            font-style: normal;
            align-items: center;
            color: #000;
            border: none;
            min-width: 240px;
            height: 64px;
            text-align: center;
            width: 100%;

            @media (min-width:$screen-md) {
              height: 100%;
              margin-left: 20px;
              width:unset;
              text-align: left;
            }
          }

          button {
            @include pop(500,16,100%,normal);
            letter-spacing: 0.01em;
            color: #FFFFFF;
            padding: 0 16px;
            gap: 40px;

            height: 56px;
            background: #27B989;
            //box-shadow: 0px 6px 20px rgba(39, 168, 125, 0.15), 0px 4px 8px rgba(39, 168, 125, 0.2025), 0px 1px 3px rgba(39, 168, 125, 0.4);
            border-radius: 6px;
            cursor: pointer;
            transition: 0.4s;
            width: 100%;

            @media (min-width:$screen-md) {
              width: 218px;
            }

            &:hover {
              background: #24A87D;
            }
          }

        }

        .email_validation {
          @include pop(400,16,100%,normal);
          color: red;
          margin-top: 20px;
          text-align: center;
        }



      }

      .date_block {
          width: 100%;

        .overlay {
          position: absolute;
          width: 100%;
          height: calc(100% - 50px);
          display: flex;
          align-items: center;
          justify-content: center;
          top: 50px;
          left: 0;
          background: #f1f3f7;

          .spinner {
            animation: rotate 2s linear infinite;
            z-index: 2;
            position: absolute;
            top: 50%;
            left: 50%;
            margin: -25px 0 0 -25px;
            width: 50px;
            height: 50px;

            & .path {
              stroke: #42247f;
              stroke-linecap: round;
              animation: dash 1.5s ease-in-out infinite;
            }

          }

          @keyframes rotate {
            100% {
              transform: rotate(360deg);
            }
          }

          @keyframes dash {
            0% {
              stroke-dasharray: 1, 150;
              stroke-dashoffset: 0;
            }
            50% {
              stroke-dasharray: 90, 150;
              stroke-dashoffset: -35;
            }
            100% {
              stroke-dasharray: 90, 150;
              stroke-dashoffset: -124;
            }
          }


        }

      }

      .personal_info_block {

      }

      .finish_block {

      }

    }

  }

  .gform_wrapper {
    .gform_required_legend {
      display: none;
    }
    .gform_body {
      .gform_page:first-child {
        position: relative;
        .gform_page_footer {
          width: 100%;
          margin: 0 auto;
          position: absolute;
          bottom: -20px;
          left: 50%;
          transform: translateX(-50%);
          @media (min-width:$screen-md) {
            width: 490px;
          }
          input[type=button]{
            position: absolute;
            right: 4px;
            left: 4px;
            bottom: 16px;
            padding: 0;
            width: auto;
            min-width: 218px;
            @media (min-width:$screen-md) {
              left: auto;
            }
          }
        }
      }
      .gform_page:nth-child(2) {
        position: relative;
        top: 60px;
      }
      .gform_page:nth-child(3) {
        position: relative;
        top: 60px;
        width: 100%;
        margin: 0 auto;
        @media (min-width:$screen-md) {
          width: 490px;
        }
        .gform_page_fields {
          padding: 32px 24px;
          background: #fff;
          box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
        }
        .gform-field-label {
          padding-top: 10px;
          padding-bottom: 10px;
          @include pop(400,16);
        }
        legend {
          .gform-field-label {
            padding-top: 15px;
            padding-bottom: 15px;
          }
        }
        input[type=text] {
          @include fig(400,18,100%,normal);
          font-family: 'Figtree';
          font-style: normal;
          align-items: center;
          color: #000;
          border: 1.2px solid #A4AAB7;
          height: 48px !important;
          text-align: center;
          width: 100% !important;
          border-radius: 6px;


          @media (min-width:$screen-md) {
            text-align: left;
          }
        }
      }
      .gform_page_footer {
        text-align: center;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 8px;
        input[type=button], input[type=submit]{
          @include pop(500,16,100%,normal);
          letter-spacing: 0.01em;
          color: #FFFFFF;
          padding: 0 16px;
          gap: 40px;

          height: 56px;
          background: #27B989;
          //box-shadow: 0px 6px 20px rgba(39, 168, 125, 0.15), 0px 4px 8px rgba(39, 168, 125, 0.2025), 0px 1px 3px rgba(39, 168, 125, 0.4);
          border-radius: 6px;
          border: none;
          cursor: pointer;
          transition: 0.4s;
          width: 100%;

          @media (min-width:$screen-md) {
            width: 218px;
          }

          &:hover {
            background: #24A87D;
          }
          & + input {
            margin-left: 0 !important;
          }
        }
      }
    }
    .cloudInputSteEmail {
      position: relative;
      width: 100%;
      margin: 0 auto;
      display: inline-block;
      background: #FFFFFF;
      box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1), 0px 24px 60px rgba(6, 47, 125, 0.05), 0px 12px 24px rgba(27, 59, 119, 0.05);
      border-radius: 10px;
      margin: 0 auto;
      padding: 4px 4px 60px 4px;

      @media (min-width:$screen-md) {
        height: 64px;
        overflow: hidden;
        width: 490px;
        padding: 4px;
      }
      .ginput_container_email {
        height: 100%;
        input {
          @include fig(400,18,100%,normal);
          font-family: 'Figtree';
          font-style: normal;
          align-items: center;
          color: #000;
          border: none;
          min-width: 240px;
          height: 64px;
          text-align: center;
          width: 100% !important;


          @media (min-width:$screen-md) {
            height: 100%;
            margin-left: 20px;
            width:unset !important;
            text-align: left;
          }
        }
      }
      .validation_message {
        position: absolute;
        bottom: -80px;
      }
    }
    .gfield-choice-input {
      &:before {
        content: '';
        border: 1px solid #fff;
        border-radius: 5px;
        width: 16px;
        height: 16px;
        display: block;
        margin-right: 10px;
        transition: .3s ease background-color;
        outline: 4px solid #BABFC9;
      }
      &:checked {
        &:before {
          background-color: #7A50CE;
          outline-color: #7A50CE;
        }
      }
    }
  }
}

.booking {
  background: red;
}
