.features_listing {
  background: #eeeff2;
  padding: 40px 0 96px 0;

  .container  {
    padding: 0 20px;

    @media (min-width:$screen-md) {
      padding: 0 40px;
    }

  }

  .desktop {
    display: none !important;

    @media (min-width:$screen-md) {
      display: grid !important;
    }

  }

  .mobile {
    display: block !important;

    @media (min-width:$screen-md) {
      display: none !important;
    }

    .swiper-slide {
      height: auto;
      .feature {
        height: 100%;
      }
    }

    .feature {
      position: relative;

      .learn_more {
        @include pop(400, 15, 124%, normal);
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: #6436bf;
        text-decoration: none;
        position: absolute;
        bottom: 32px;
      }

    }

  }

  .features_list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;

    .feature {
      background: #fff;
      grid-column: span 1;
      padding: 32px;
      box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
      text-decoration: none;
      cursor: pointer;
      transition: 0.3s;

      a {
        text-decoration: none;
      }

      .icon {
        max-width: 80px;
        height: auto;
        margin-bottom: 36px;
      }

      h3 {
        margin-bottom: 12px;
        @include pop(400, 29, 124%, normal);
        background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        text-decoration: none;
      }

      .content {
        @include pop(400, 20, 136%, normal);
        color: #635E6E;
        max-width: 90%;
        text-decoration: none;
      }

      &:hover {
        transform: scale(1.02);
      }

    }

    .cta_block {
      background: #fff;
      grid-column: span 2;
      box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
      display: flex;
      flex-direction: column;
      overflow: hidden;
      margin-top: 150px;

      @media (min-width:$screen-md) {
        flex-direction: row;
        max-height: 340px;
        margin-top: 0;
      }

      .col {
        max-width: 100%;
        width: 100%;

        @media (min-width:$screen-md) {
          max-width: 50%;
        }

      }

      .image_col {
        order: 1;

        @media (min-width:$screen-md) {
          order: 0;
        }

        img {
          height: 100%;
          width: 140%;
        }

      }

      .content_col {
        padding: 32px;
        order: 0;

        @media (min-width:$screen-md) {
          padding: 84px 120px 0 45px;
        }

        .icon {
          margin-bottom: 24px;
          height: 32px;
          width: auto;
        }

        h3 {
          @include pop(400, 29, 124%, normal);
          margin-bottom: 24px;
          background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
          background-clip: text;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }

        a {
          @include pop(400, 17, 124%, normal);
          color: #6436BF;
          text-transform: uppercase;
          text-decoration: none;
        }

      }

    }

  }

}
