.page_take_over_banner {
  position: relative;
  height: 80vh;
  background: #201632;

  background-repeat: no-repeat;
  background-position: 80% 0;
  background-size: cover;
  padding: 135px 0 0;
  overflow: hidden;

  @media (min-width: $screen-md) {
    height: 100vh;
    padding: 151px 0 0;
  }

  #take_over_banner_gradient {
    position: absolute;
    height: 70vh;
    left: -356px;
    right: -375px;
    bottom: -25%;
    background: linear-gradient(0deg, #6436BF 65%, #201632 85%);
    //background: #e75524;
    //filter: blur(120px);
    transition: ease all 0.3s;
  }

  h3 {
    @include pop(400, 35, 43px);
    letter-spacing: -0.02em;
    text-align: center;
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.25) 0%,
      #ffffff 0.01%,
      rgba(255, 255, 255, 0.54) 100%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;

    @media (min-width: $screen-md) {
      font-size: 44px;
      line-height: 49px;
    }

    br {
      display: none;
      @media (min-width: $screen-md) {
        display: block;
      }
    }
  }

  .page_take_over_slider_dots {
    margin-top: 49px;
    z-index: 5;
    position: relative;

    .swiper-wrapper {
      justify-content: center;
    }
    .swiper-slide {
      display: flex;
      transition: ease all 0.3s;
      width: 26px;
      height: 26px;
      align-items: center;
      justify-content: center;
      height: auto;
      max-width: 42px;

      &:hover {
        cursor: pointer;
      }

      span {
        width: 16px;
        height: 16px;
        border-radius: 100px;
      }

      &.swiper-slide-thumb-active {
        transition: ease all 0.3s;

        border-radius: 100px;
        span {
          border: 3px solid #ffffff;
          width: 26px;
          height: 26px;
        }
      }
    }
  }

  .page_take_over_slider_images {
    z-index: 1;
    position: relative;
    @media (min-width: $screen-md) {
      margin-top: 32px;
    }

    img {
      width: auto;
      height: 80vh;
      margin-left: -80%;
      margin-top: -80px;
      @media (min-width: $screen-sm) {
        // margin-top: -200px;
        margin-top: -150px;
        margin-left: 0;
        height: auto;
        width: 100%;
      }

      @media (min-width: $screen-lg) {
        margin-top: -200px;
      }
    }
  }
}

