.full_width_cta {
  background: #eeeff2;
  padding: 60px 0;

  .container {
    padding: 0;
    max-width: 1400px;

    @media (min-width: $screen-md) {
      padding: 0 20px;
    }

  }

  h2 {
    @include pop(400, 29, 124%, normal);
    background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    margin-bottom: 35px;
    text-align: center;
    letter-spacing: -0.58px;


    @media (min-width: $screen-md) {
      @include pop(400, 35, 43.4px, normal);
      letter-spacing: -1.06px;

      br {
        display: none;
      }

    }

  }

  .link {
    display: flex;
    width: 100%;
    align-items: center;
    align-content: center;
    justify-content: center;
    position: relative;
    gap: 32px;

    &:before {
      content: '';
      width: 100%;
      height: 1px;
      background: #D1D5DB;
    }

    &:after {
      content: '';
      width: 100%;
      height: 1px;
      background: #D1D5DB;
    }

    a {
      padding: 16px 32px;
      border-radius: 6px;
      height: 64px;
      color: #fff;
      background: #6436BF;
      @include pop(500, 16, 32px, normal);
      text-decoration: none;
      transition: 0.3s;
      white-space: nowrap;

      &:hover {
        background: #5728ad;
      }

    }

  }

}