.brand_block {
  padding: 93px 0 0;
  overflow: hidden;
  @media (min-width: $screen-md) {
    padding: 84px 0 0;
  }

  .brand_wrapper {
    flex-direction: column;
    margin-bottom: 50px;
    @media (min-width: $screen-md) {
      flex-direction: row;
      align-items: center;
      margin-bottom: 0;
    }
  }

  .brand_block_col {
    width: 100%;
  }

  .col_left {
    margin-bottom: 43px;
    @media (min-width: $screen-md) {
      margin-bottom: 0;
    }
    img {
      width: 170%;
      margin-left: -20%;
      @media (min-width: $screen-md) {
        margin-left: 0;
        width: 150%;
      }
    }
  }

  .col_right {
    .big_title {
      @include pop(400, 35, 43px);
      @include titleGrad();
      margin-bottom: 12px;
      letter-spacing: -0.02em;
      @media (min-width: $screen-md) {
      }
    }

    .small_title {
      @include fig(400, 17, 26px);
      letter-spacing: -0.02em;
      margin-bottom: 24px;
      text-transform: uppercase;
      color: #1abc87;
    }
    .description {
      max-width: 551px;
      p {
        @include fig(400, 18, 28px);
        color: #4f4b58;
        margin-bottom: 16px;

        img {
          padding-top: 16px;
          width: auto;
          height: auto;
        }
      }
    }
  }

  .partner_section {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 96px 0;
    @media (min-width: $screen-md) {
      padding: 80px 0;
    }

    .title_area {
      margin-bottom: 40px;
      @media (min-width: $screen-md) {
        margin-bottom: 24px;
      }
    }

    h3 {
      @include pop(400, 23, 31px);
      letter-spacing: -0.02em;
      @include titleGrad();
      text-align: center;
      @media (min-width: $screen-md) {
        font-size: 18px;
        line-height: 22px;
        text-align: left;
      }
    }

    .partner_logos {
      display: flex;
      flex-wrap: wrap;
      @media (min-width: $screen-md) {
        flex-wrap: nowrap;
        width: 100%;
        margin-top: 40px;
      }
    }

    .logo {
      width: 100%;
      max-width: 50%;
      display: flex;
      align-items: center;
      margin-bottom: 40px;
      // justify-content: center;

      @media (min-width: $screen-md) {
        justify-content: center;
      }


      img {
        width: auto;
        max-height: 28px;
      }
    }
  }
}

